import React, { useEffect, useState } from "react";import ListDrop from "./ListDrop";import { getDataEvent } from "../../../../services/events";import { IntegrationList } from "./index";import ErrorMessage from "../../../Components/ErrorMessage/ErrorMessage";import { REACT_APP_BASE_URL } from "../../../../constants";import ModifyLead from "./ModifyLead/ModifyLead";import { checkPermission } from "../../../../services/utils";import { FormattedMessage, useIntl } from "react-intl";import { Permissions } from "../../../Components";import CustomToast from "../../../Components/CustomToast/CustomToast";export default function LeadAdded({  toggle,  leadsLabels,  isLeadAdded,  labelId,  lead,  addToList,  onLeadUndo,  onLeadEdit,  credits,  memberPermissions,  isPluginTop,  checkPlanPermissions,  checkMemberPermissions,  remainingCredits,  isVersion2,}) {  const [show, setShow] = useState(false);  const [showIntegrations, setShowIntegrations] = useState(false);  const [showModifyLead, setShowModifyLead] = useState(false);  const [labelName, setLabelName] = useState(false);  const [listChanged, setListChanged] = useState(false);  const [exportSuccess, setExportSuccess] = useState(false);  const [permission, setPermission] = useState(false);  const [error, setError] = useState(false);  const intl = useIntl();  useEffect(() => {    setTimeout(() => {      setShow(true);    }, 0);  }, []);  useEffect(() => {    if (leadsLabels) {      const label = leadsLabels.find((label) => label.id == labelId);      if (label) setLabelName(label.name);      else setLabelName(null);    }    if (lead.lead.statusCode == 200) {      setListChanged(true);      setTimeout(() => {        setListChanged(false);      }, 500);    }  }, [labelId]);  return (    <>      <div        className={"go-to-leads step4 " + (show ? "" : "dnone")}        style={isPluginTop ? {} : toggle ? {} : { display: "none" }}      >        <div className="leads-in">          <div className="gtl-icon">            <span className="">              <svg                width="12"                height="9"                viewBox="0 0 12 9"                fill="none"                xmlns="http://www.w3.org/2000/svg"              >                <path                  d="M1.5 4.62L4.41892 7.5L10.5 1.5"                  stroke="#566DF2"                  stroke-width="2"                  stroke-linecap="round"                  stroke-linejoin="round"                />              </svg>            </span>          </div>          <div>            <p className="black">              <FormattedMessage                id={"leadAddedInYour"}                values={{                  list: (                    <span className="blue fw700">                                            {labelName                        ? labelName                        : intl.formatMessage({ id: "defaultList" })}                                          </span>                  ),                  link: (                    <a                      href={REACT_APP_BASE_URL + "/leads"}                      target={"_blank"}                      style={{ textDecoration: "underline" }}                    >                      <FormattedMessage id={"list"} />                    </a>                  ),                }}              />            </p>          </div>        </div>        <div          style={{            width: 100,            display: "flex",            marginLeft: -50,            top: 35,            position: "relative",          }}        >          {!checkPlanPermissions("Edit") || !checkMemberPermissions("Edit") ? (            <Permissions.CallToAction              isPlan={!checkPlanPermissions("Edit")}              withoutIcon={true}              toolTipPosition={"left"}              toolTipStyle={{ width: 200 }}            >              <button className="btn-gray xs" style={{ marginRight: 7 }}>                <img                  src={window.chrome.runtime.getURL(                    "assets/images/user-edit-solid.svg"                  )}                  style={{ width: 16 }}                />              </button>            </Permissions.CallToAction>          ) : (            <button              className="btn-gray xs"              onClick={() => setShowModifyLead(true)}              style={{ marginRight: 7 }}            >              <img                src={window.chrome.runtime.getURL(                  "assets/images/user-edit-solid.svg"                )}                style={{ width: 16 }}              />            </button>          )}          {!checkPlanPermissions("Edit") || !checkMemberPermissions("Edit") ? (            <Permissions.CallToAction              withoutIcon={true}              isPlan={!checkPlanPermissions("Edit")}              toolTipPosition={"left"}              toolTipStyle={{ width: 200 }}            >              <button className="btn-gray xs">                <img                  src={window.chrome.runtime.getURL(                    "assets/images/trash-alt-regular.svg"                  )}                  style={{ width: 12 }}                />              </button>            </Permissions.CallToAction>          ) : (            <button className="btn-gray xs" onClick={onLeadUndo}>              <img                src={window.chrome.runtime.getURL(                  "assets/images/trash-alt-regular.svg"                )}                style={{ width: 12 }}              />            </button>          )}        </div>        <div          className="lead-btns"          style={{ display: "flex", justifyContent: "center", width: "183px" }}        >          <div>            {listChanged && (              <div                className="gtl-icon"                style={{                  position: "absolute",                  right: 116,                  top: 38,                  width: 20,                  background: "rgb(86, 109, 242)",                }}              >                <span className="">                  <svg                    width="12"                    height="9"                    viewBox="0 0 12 9"                    fill="none"                    xmlns="http://www.w3.org/2000/svg"                  >                    <path                      d="M1.5 4.62L4.41892 7.5L10.5 1.5"                      stroke="#fff"                      stroke-width="2"                      stroke-linecap="round"                      stroke-linejoin="round"                    />                  </svg>                </span>              </div>            )}            <ListDrop              className={"btn xs"}              defaultLabel={intl.formatMessage({ id: "changeList" })}              list={leadsLabels}              style={{ marginRight: 10, width: "max-content" }}              //  onSelectItem={addToList}              onSelectItem={(value) =>                !checkPlanPermissions("Save")                  ? setPermission("isPlan")                  : !checkMemberPermissions("Save")                  ? setPermission("isOrg")                  : addToList(value)              }              value={labelName}            />          </div>          <button className="btn-white2 xs" onClick={setShowIntegrations}>            <FormattedMessage id={"export"} />          </button>          {exportSuccess == "success" && (            <CustomToast              toggle              isPortal              style={{                background: "rgb(14, 183, 99)",                color: "white",                /* top: 250,                height: 45,*/              }}              onClose={() => setExportSuccess(null)}            >              <span style={{ color: "#FFF" }}>                <FormattedMessage                  id={"successfullyExported"}                  defaultMessage={"Successfully exported"}                />{" "}              </span>            </CustomToast>          )}          {/* {exportSuccess !== "success" && (            <div              className="gtl-icon"              style={{                position: "absolute",                right: 50,                top: 38,                width: 20,                background: "rgb(86, 109, 242)",              }}            >              <span className="">                <svg                  width="12"                  height="9"                  viewBox="0 0 12 9"                  fill="none"                  xmlns="http://www.w3.org/2000/svg"                >                  <path                    d="M1.5 4.62L4.41892 7.5L10.5 1.5"                    stroke="#fff"                    stroke-width="2"                    stroke-linecap="round"                    stroke-linejoin="round"                  />                </svg>              </span>            </div>          )}*/}          {exportSuccess == "error" && (            <img              src={window.chrome.runtime.getURL("assets/images/error-icon.png")}              style={{                position: "absolute",                right: 116,                top: 38,                width: 20,              }}            />          )}          <div            className={              "pluginSection-body profile-body " +              (showIntegrations ? "show" : "")            }            id="profile-body"            style={{              marginTop: isPluginTop ? "-3px" : "10px",              minHeight: showIntegrations ? "80%" : 0,              overflow: isPluginTop ? "auto" : "inherit",              zIndex: 1,            }}          >            <div class={"integrations-header"} style={{ alignItems: "center" }}>              <h3 style={{ fontSize: 17 }}>                <FormattedMessage id={"exportToIntegrations"} />                {isVersion2 &&                  remainingCredits &&                  remainingCredits.organizationCredits && (                    <div                      className={"pushes-remaining "}                      style={{                        marginLeft: 0,                        width: "max-content",                        color:                          remainingCredits.organizationCredits.exportCredits ==                          0                            ? "rgb(180,0,0)"                            : "",                      }}                    >                      <i className="fas fa-registered"></i>                      {remainingCredits.organizationCredits.exportCredits ==                      -1 ? (                        <img                          src={window.chrome.runtime.getURL(                            "/assets/images/infinity-solid.svg"                          )}                          style={{ width: 20, marginRight: 6 }}                        />                      ) : (                        remainingCredits.organizationCredits.exportCredits                      )}{" "}                      Exports{" "}                      {remainingCredits.organizationCredits.exportCredits == -1                        ? ""                        : "remaining"}                    </div>                  )}              </h3>              <button                className="btn-white2 xs"                onClick={() => {                  setError(null);                  setShowIntegrations(false);                }}                style={{ height: 30 }}              >                <div>                  <img                    src={window.chrome.runtime.getURL(                      "assets/images/arrow.svg"                    )}                  />                </div>                <FormattedMessage id={"back"} />              </button>            </div>            <IntegrationList              setToggle={() => setShowIntegrations(false)}              leads={[lead.lead]}              onSuccess={setExportSuccess}              memberPermissions={memberPermissions}              credits={credits}              setPermission={setPermission}              error={error}              setError={setError}            />          </div>          <div            className={              "pluginSection-body profile-body modify-lead-wrapper " +              (showModifyLead ? "show" : "")            }            id="profile-body"            style={{              marginTop: "7%",              minHeight: showModifyLead ? "80%" : 0,              zIndex: 1,            }}          >            <div className={"integrations-header"}>              <h3>                <FormattedMessage id={"editLead"} />              </h3>              <button                className="btn-white2 xs"                onClick={() => setShowModifyLead(false)}              >                <div>                  <img                    src={window.chrome.runtime.getURL(                      "assets/images/arrow.svg"                    )}                    alt=""                  />                </div>              </button>            </div>            {(checkMemberPermissions("Edit") ||              checkMemberPermissions("Edit")) && (              <ModifyLead                lead={lead}                setShowModifyLead={() => setShowModifyLead(false)}              />            )}          </div>        </div>      </div>      {/* {permission && <ErrorMessage message={permission} />}*/}      {permission && (        <Permissions.Box          show={permission}          setShow={setPermission}          isPlan={permission === "isPlan"}        />      )}    </>  );}