import React, { useEffect, useState } from "react";import PointsLoading from "../../../Components/PointsLoading/PointsLoading";import { FormattedMessage } from "react-intl";import { Permissions } from "../../../Components";export default function ({  onShowEmails,  onAdvanceSearch,  discovered,  noPhones,  errorMessage,  loading,  isWebsiteSupported,  displayFromfakeProfile,}) {  const [hide, setHide] = useState(false);  /*  useEffect(() => {    if (displayFromfakeProfile) {      setHide(false);    }  }, [displayFromfakeProfile]);*/  const handleShowEmails = () => {    setHide(true);    setTimeout(() => {      onShowEmails();    }, 0);  };  return (    <>      {!discovered && (        <div          className={            "btn-in btn-user searching-contact-info  " + (hide ? "dnone" : "")          }        >          {isWebsiteSupported ? (            <button className="btn-white2 sm step1" onClick={handleShowEmails}>              <span className="gtext">                <FormattedMessage id={"showEmails"} />              </span>{" "}              <span className="credit-info magenta">                {" "}                <FormattedMessage id={"showDetails"} /> 1{" "}                <FormattedMessage id={"credits"} />              </span>{" "}            </button>          ) : (            <Permissions.CallToAction              withoutIcon={true}              isPlan={true}              toolTipStyle={{ width: "100%" }}            >              <button className="btn-white2 sm step1">                <span className="gtext">                  <FormattedMessage id={"showEmails"} />                </span>{" "}                <span className="credit-info magenta">                  {" "}                  <FormattedMessage id={"showDetails"} /> 1{" "}                  <FormattedMessage id={"credits"} />                </span>{" "}              </button>            </Permissions.CallToAction>          )}        </div>      )}      {noPhones ||      errorMessage ==        "You are blocked from using the Kaspr advanced LinkedIn Search feature" ? (        <div          style={{            textAlign: "center",            margin: "19px 0",            color: "rgb(236, 59, 176)",            fontSize: 13,          }}        >          <FormattedMessage id={"weCouldNtFindContactPhones"} />        </div>      ) : (        <div className="btn-in btn-user">          {loading ? (            <button              className="btn sm"              style={{                display: "block",                height: 40,                overflow: "hidden",              }}            >              <img                style={{                  height: 49,                  overflow: "hidden",                  position: "relative",                  top: -17,                }}                src={window.chrome.runtime.getURL(                  "assets/images/pointsLoading.svg"                )}                alt=""              />            </button>          ) : isWebsiteSupported ? (            <button className="btn sm step2" onClick={onAdvanceSearch}>              <FormattedMessage id={"advancedSearch"} />              <span className="credit-info">                <FormattedMessage id={"showDetails"} /> {discovered ? "1" : "2"}{" "}                <FormattedMessage id={"credits"} />                {!discovered && "s"}              </span>{" "}            </button>          ) : (            <Permissions.CallToAction              withoutIcon={true}              buttonClassName={"btn sm step2"}              isPlan={true}              toolTipStyle={{ width: "100%" }}            >              <button className="btn sm step2">              <FormattedMessage id={"advancedSearch"} />              <span className="credit-info">                <FormattedMessage id={"showDetails"} /> {discovered ? "1" : "2"}{" "}                <FormattedMessage id={"credits"} />                {!discovered && "s"}              </span>{" "}              </button>            </Permissions.CallToAction>          )}          <p className="magenta" style={{ color: "#999", fontWeight: 400 }}>            <span className="info" style={{ background: "#ccc" }}>              <svg                width="4"                height="10"                viewBox="0 0 4 10"                fill="none"                xmlns="http://www.w3.org/2000/svg"              >                <path                  d="M2.16992 1.3H2.29492"                  stroke="#fff"                  stroke-width="2.1"                  stroke-linecap="round"                  stroke-linejoin="round"                />                <path                  d="M2.2998 8.8V4.8H1.2998"                  stroke="#fff"                  stroke-width="2"                  stroke-linecap="round"                  stroke-linejoin="round"                />              </svg>            </span>{" "}            <FormattedMessage              id={"advancedSearchFocusesOnFindingPhoneNumbers"}              values={{                strong: (chnk) => <strong>{chnk}</strong>,              }}            />          </p>        </div>      )}    </>  );}