import React, { useEffect, useRef, useState } from "react";import { REACT_APP_BASE_URL } from "../../../constants";import { FormattedMessage } from "react-intl";export default function Header({  onProfileClick,  remainingCredits,  credits,  userInfos,}) {  const [creditCollapse, setCreditCollapse] = useState(false);  const ref = useRef(null);  useEffect(() => {    document.addEventListener("mousedown", handleClickOutside);    return () => document.removeEventListener("mousedown", handleClickOutside);  }, []);  function handleClickOutside(e) {    if (ref.current && !ref.current.contains(e.target)) {      setCreditCollapse(false);    }  }  return (    <nav className="ps-nav">      <div className="logo">        <button>          <img            className="img-fluid"            src={window.chrome.runtime.getURL("/assets/images/logo_on_white.png")}            alt={""}            width={80}          />        </button>      </div>      <div className="ps-drop-profile" style={{ display: "block" }}>        {/*   {remainingCredits &&          !isNaN(remainingCredits.organizationRemaining) &&          !isNaN(remainingCredits.personalCredits) && (            <div              className={"creditDropdown " + (creditCollapse ? "show" : "")}              onClick={() => setCreditCollapse(!creditCollapse)}              ref={ref}            >              <button>                <span className="ps-dro-img">                  <img                    src={window.chrome.runtime.getURL(                      "/assets/images/creditSm.svg"                    )}                    alt={""}                  />                </span>{" "}                {remainingCredits.organizationRemaining +                  remainingCredits.personalCredits}{" "}                <FormattedMessage id={"credit(s)"} />              </button>              <div className="credit-dropdown">                <div className="cd-in">                  <div className="ct-count">                    <FormattedMessage id={"personal"} />{" "}                    <span>{remainingCredits.personalCredits}</span>                  </div>                  <div className="ct-count">                    <FormattedMessage id={"team"} />{" "}                    <span>{remainingCredits.organizationRemaining}</span>                  </div>                </div>              </div>            </div>          )}*/}        <div className={"getUnlimited-and-settings"}>          {((credits &&            credits.organizationCredits &&            !credits.organizationCredits.subscription &&            credits.personalCredits &&            credits.personalCredits.recurrenceCredits &&            !credits.personalCredits.recurrenceCredits["workEmailCredits"]              .isUnlimited) ||            !credits || !credits.organizationCredits) && (            <a              href={REACT_APP_BASE_URL + "/onboarding/invite"}              target={"_blank"}              className={"header-getUnlimited"}            >              <FormattedMessage id={"extension.getUnlimitedB2bCredits"} />              <span className={"header-ln"}></span>            </a>          )}          {(userInfos.nextStep === -1 || userInfos.nextStep > 2) && (            <div className="profile-picture ">              <button onClick={() => onProfileClick(true)}>                <img                  className={"step5"}                  src={                    userInfos.image ||                    window.chrome.runtime.getURL("/assets/images/user.png")                  }                  alt={""}                  style={{ borderRadius: "50%", width: 35 }}                />                <span>=</span>              </button>            </div>          )}        </div>      </div>    </nav>  );}