import React, { useEffect, useState } from "react";export default function AlreadyOpened({ toggle }) {  const [show, setShow] = useState(false);  useEffect(() => {    setTimeout(() => {      setShow(true);    }, 0);  }, []);  return (    <div      className={"already-opened  " + (show ? "" : "dnone")}      style={toggle ? {} : { display: "none" }}    >      <p className="blue">        <span>          <svg            width="14"            height="10"            viewBox="0 0 14 10"            fill="none"            xmlns="http://www.w3.org/2000/svg"          >            <path              d="M1 5.16L4.89189 9L13 1"              stroke="#566DF2"              stroke-width="2"              stroke-linecap="round"              stroke-linejoin="round"            />          </svg>        </span>{" "}        Youve already opened this information.      </p>    </div>  );}