function getCurrentUrl() {  return new Promise((resolve) => {    window.chrome.tabs.getSelected(null, async (tab) => {      resolve(tab.url);    });  });}function getDataEvent(action, input) {  return new Promise((resolve) => {    window.chrome.runtime.sendMessage({ action, input }, async (data) => {      resolve(data);    });  });}export { getCurrentUrl, getDataEvent };