import React from "react";export default function AutomaticDiscoveringCheckBox({  checked,  img,  colorClass,  title,  onCheck,  type}) {  return (    <div className={"section-line"} onClick={()=> onCheck("AUTO_DISCOVER",type) }>      <div className="checkbox">        <input type="checkbox" checked={checked} readOnly />        <div className="cBox">          <div>            <img src={window.chrome.runtime.getURL(img)} />            <span className={colorClass}> {title} </span>          </div>        </div>      </div>    </div>  );}