import React from "react";import { REACT_APP_BASE_URL } from "../../../constants";import { FormattedMessage } from "react-intl";export default function Login() {  return (    <section className=" login-sec" id="ps-main">      <div className="pluginSection-body">        <div className="login-head">          <img src={window.chrome.runtime.getURL("assets/images/logo_on_white.png")} width={110} />        </div>        <div className="login-body" style={{ marginTop: 125 }}>          <h3>            <FormattedMessage id={"aBetterWayToSearch"} />          </h3>          <p>            <FormattedMessage id={"getAccessToYourProspects"} />          </p>          <a            href={              REACT_APP_BASE_URL +              "/signin?utm_source=Widget&utm_medium=Connect"            }            className="btn"            target={"_blank"}            style={{ color: "white", textDecoration: "none" }}          >            <FormattedMessage id={"haveAnAccount"} />            <span className="ml-1">              <FormattedMessage id={"logIn"} />{" "}              <img                src={window.chrome.runtime.getURL(                  "assets/images/linkWhite.svg"                )}              />            </span>          </a>          <a            className="link"            href={              REACT_APP_BASE_URL +              "/signup?utm_source=Widget&utm_medium=Connect"            }            target={"_blank"}            style={{ color: "#737DA5", textDecoration: "none" }}          >            <FormattedMessage id={"newToKaspr"} />{" "}            <span>              <FormattedMessage id={"signup"} />{" "}              <img                src={window.chrome.runtime.getURL("assets/images/linkBlue.svg")}              />            </span>          </a>        </div>      </div>    </section>  );}