import React, { useEffect, useState } from "react";import Copy from "../../../Components/Copy/Copy";import Rating from "./Rating";import {  FaThumbsDown,  FaThumbsUp,  FaChevronDown,  FaChevronUp,} from "react-icons/fa";import { FormattedMessage } from "react-intl";import HiddenPersonalEmails from "../Common/HiddenPersonalEmails";import CustomToolTip from "../../../Components/CustomToolTip/CustomToolTip";import { REACT_APP_BASE_URL } from "../../../../constants";export default function ContactBox({  title,  data,  type,  currentProEmail,  currentPersonalEmail,  discovered,  hiddenPersonalEmailsNumber,  creditType,  autoDiscover,  onDiscover,  noMoreCredits,  showAdvancedSearch,  oneDiscovered,  isVersion2,  isWebsiteSupported,  error,  loading,}) {  const [showAll, setShowAll] = useState(false);  const [selectedItem, setSelectedItem] = useState(null);  if (!data) return <div />;  useEffect(() => {    if (data.length > 1) {      setShowAll(true);    }  }, []);  const selectItem = (item) => {    if (selectedItem === item) setSelectedItem(null);    else setSelectedItem(item);  };  let credits = {    phoneCredits: { img: "phone-credits.svg", color: "#0EB763" },    personalEmailCredits: { img: "direct-email-credits.svg", color: "#FF206E" },    workEmailCredits: { img: "work-email-credits.svg", color: "#4D58F3" },  };  const DiscoverButton = () => {    if (!isVersion2) return null;    if (!!noMoreCredits || !isWebsiteSupported) {      return (        <CustomToolTip          element={<FormattedMessage id={"youHaveNoCreditsLeft"} />}          position={"left"}          style={{ width: 200 }}        >          <a            className="upgrade-btn"            href={REACT_APP_BASE_URL + "/billing/plans"}            target={"_blank"}          >            <img              src={window.chrome.runtime.getURL("/assets/images/premium.svg")}            />            <span>              <FormattedMessage id={"upgrade"} />            </span>          </a>        </CustomToolTip>      );    }    if (      !autoDiscover[creditType] ||      (showAdvancedSearch && type == "phones") ||      oneDiscovered    ) {      return (        <button          style={{            borderColor: credits[creditType].color,            color: credits[creditType].color,          }}          className={"show-btn"}          onClick={() => onDiscover([creditType])}        >          {loading ? (            <img              style={{                width: 16,                margin: 2,                marginRight: 7,              }}              src={window.chrome.runtime.getURL("/assets/images/loader.gif")}            />          ) : (            <img              src={window.chrome.runtime.getURL(                "/assets/images/" + credits[creditType].img              )}            />          )}          <span>            <FormattedMessage id={"show"} />          </span>        </button>      );    }    return null;  };  const Data = () => {    if (!!hiddenPersonalEmailsNumber && !!!data.length) {      return (        <HiddenPersonalEmails          hiddenPersonalEmailsNumber={hiddenPersonalEmailsNumber}        />      );    }    if (data.length <= 2) {      return data.map((item, i) => (        <div>          {discovered ? (            <Item              item={item}              star={item == currentPersonalEmail || item == currentProEmail}              discovered={discovered}              selectedItem={selectedItem}              selectItem={selectItem}              creditType={creditType}            />          ) : (            <p className="user-info">              {item}              {i == 0 && <DiscoverButton />}{" "}            </p>          )}        </div>      ));    } else {      if (showAll) {        return (          <div>            {data.slice(0, 2).map((item, i) => (              <div>                {discovered ? (                  <Item                    item={item}                    star={                      item == currentPersonalEmail || item == currentProEmail                    }                    discovered={discovered}                    selectedItem={selectedItem}                    selectItem={selectItem}                  />                ) : (                  <p className="user-info">                    {item}                    {i == 0 && <DiscoverButton />}                  </p>                )}              </div>            ))}            <button className="more-info" onClick={() => setShowAll(!showAll)}>              <FormattedMessage id={"showMore"} /> {type}            </button>          </div>        );      }      return (        <div>          {data.map((item, i) => (            <div>              {discovered ? (                <>                  <Item                    item={item}                    star={                      item == currentPersonalEmail || item == currentProEmail                    }                    discovered={discovered}                    selectedItem={selectedItem}                    selectItem={selectItem}                    creditType={creditType}                  />                  {!!hiddenPersonalEmailsNumber && (                    <HiddenPersonalEmails                      hiddenPersonalEmailsNumber={hiddenPersonalEmailsNumber}                    />                  )}                </>              ) : (                <p className="user-info">                  {item}                  {i == 0 && <DiscoverButton />}{" "}                </p>              )}            </div>          ))}          <button className="more-info" onClick={() => setShowAll(!showAll)}>            - <FormattedMessage id={"hide"} />{" "}            <span style={{ position: "relative", top: 2 }}>^</span>          </button>        </div>      );    }  };  return (    <ContactBoxWrapper      type={type}      title={title}      showPhones={        showAdvancedSearch && (          <span className={"user-info"}>            <DiscoverButton />          </span>        )      }    >      {error || <Data />}    </ContactBoxWrapper>  );}function Item({ item, star, discovered, selectedItem, selectItem }) {  return (    <div style={{ margin: "3px 0" }}>      <div style={{ "text-align": "center", display: "inline" }}>        <p          style={            star              ? { display: "inline-block", marginLeft: -21 }              : { display: "inline-block" }          }          className="user-info"        >          <span className={star ? "star" : ""}>            {discovered ? <Copy>{item}</Copy> : item}          </span>        </p>        {discovered && (          <button            className="btn-gray xs"            style={{ width: 50, padding: "0", float: "right" }}            onClick={() => {              selectItem(item);            }}          >            <span>              <span style={{ margin: "1px", color: "#bfb9cf" }}>                <FaThumbsUp className={"fa-3x"} fontSize={10} />              </span>              <span style={{ margin: "1px", color: "#bfb9cf" }}>                <FaThumbsDown className={"fa-3x"} fontSize={10} />              </span>            </span>            <span style={{ margin: 0, marginLeft: 3, color: "#ccc" }}>              {selectedItem === item ? (                <FaChevronUp className={"fa-3x"} fontSize={8} />              ) : (                <FaChevronDown className={"fa-3x"} fontSize={8} />              )}            </span>          </button>        )}        <br />      </div>      {discovered && selectedItem === item && <Rating item={item} />}    </div>  );}function ContactBoxWrapper({  children,  type,  title,  showProEmails,  showPhones,}) {  return (    <div      className="contact-box"      style={!!showPhones ? { alignItems: "center" } : {}}    >      <div className="cb-icon">        <img          className=""          src={window.chrome.runtime.getURL(            "/assets/images/" +              (type === "emails" ? "emailIcon.svg" : "phoneIcon.svg")          )}          alt={""}        />      </div>      <div className="cb-text">        <p          className=""          style={{            marginBottom: 10,            marginTop: 5,            display: "flex",            justifyContent: "space-between",            alignItems: "center",          }}        >          {title} {showPhones}        </p>        {children}      </div>    </div>  );}