import React from "react";export default function CompanyEnrichment({ data }) {  const HiddenData = () => {    const Pattern = ({ pattern }) => <li>{pattern}</li>;    const Phone = ({ phone }) => <li> {phone}</li>;    return (      <>        <ul>          {data.patterns.map((pattern) => (            <Pattern pattern={pattern} />          ))}        </ul>        <ul>          {data.phones.map((phone) => (            <Phone phone={phone} />          ))}        </ul>      </>    );  };  const DiscoveredData = () => {    const Pattern = ({ pattern }) => <li>{pattern}</li>;    const Phone = ({ phone }) => <li> {phone}</li>;    return (      <>        <ul>          <li>Patterns :</li>          {data.patterns.map((pattern) => (            <Pattern pattern={pattern.pattern} />          ))}        </ul>        <ul>          {data.phones.map((phone) => (            <Phone phone={phone} />          ))}        </ul>      </>    );  };  if (!data) return <div />;  return data.hidden ? <HiddenData /> : <DiscoveredData />;}