import React, { useEffect, useRef, useState } from "react";export default function CustomSelect({  title,  items,  onSelectItem,  value,  style,  dropDownStyle,  formInStyle,  setNewItems,}) {  const [toggle, setToggle] = useState(false);  const ref = useRef(null);  useEffect(() => {    document.addEventListener("mousedown", handleClickOutside);    return () => document.removeEventListener("mousedown", handleClickOutside);  }, []);  function handleClickOutside(e) {    if (ref.current && !ref.current.contains(e.target)) {      setToggle(false);    }  }  const onDropDown = (e) => {    e.preventDefault();    setToggle(!toggle);  };  const handleChooseItem = (item) => {    onSelectItem(item);    setTimeout(() => {      setToggle(false);    }, 10);  };  const Item = ({ label, index }) => {    const [inputToggle, setInputToggle] = useState(null);    const [inputField, setInputField] = useState(label);    const handleEditField = () => {      let fields = Object.assign([], items);      fields[index] = inputField;      setNewItems(fields);      setInputToggle(false);    };    const handleDeleteField = (value) => {      let fields = Object.assign([], items);      fields = fields.filter((field) => field !== value);      setNewItems(fields);    };    return (      <div className="cdi-in" style={{ cursor: "default" }}>        <input type="radio" name="List" />        <p          className="mb-0"          style={{            display: "flex",            padding: "0 10px",            justifyContent: "space-between",          }}        >          {inputToggle == label ? (            <div className={"select-edit-field"}>              <input                type="text"                value={inputField}                onChange={(e) => setInputField(e.target.value)}              />              <button onClick={handleEditField}>ok</button>            </div>          ) : (            <span              style={{ cursor: "pointer" }}              onClick={() => handleChooseItem(label)}            >              {label}            </span>          )}          {inputToggle !== label && (            <div>              <img                src={window.chrome.runtime.getURL(                  "assets/images/edit-solid.svg"                )}                style={{                  width: 16,                  cursor: "pointer",                  marginRight: 7,                }}                alt=""                onClick={() => setInputToggle(label)}              />              <img                src={window.chrome.runtime.getURL(                  "assets/images/trash-alt-regular.svg"                )}                style={{                  width: 12,                  cursor: "pointer",                }}                alt=""                onClick={() => handleDeleteField(label)}              />            </div>          )}        </p>      </div>    );  };  const Items = () => {    return (      <>        {items.map((label, i) => (          <Item label={label} index={i} />        ))}      </>    );  };  return (    <div className="form-in" ref={ref} style={formInStyle || {}}>      <label>{title}</label>      <div className={"custom-dropdown workflow " + (toggle ? "show" : "")}>        <button className="custom-btn" onClick={onDropDown} style={style || {}}>          {value}        </button>        <div          className="custom-dropdown-in drop-down-style "          style={dropDownStyle || {}}        >          <Items />        </div>      </div>    </div>  );}