import React from "react";export default function RemainingCreditsSection({  remainingCredits,  credits,  isVersion2,}) {  if (!remainingCredits.personalCredits) return <div />;  let personalCredits = remainingCredits.personalCredits;  let organizationCredits = remainingCredits.organizationRemaining;  if (!isVersion2) {    return (      <div className={"section-line-flx"}>        <RemainingCreditItem          icon={"legacy-credits.svg"}          credit={            (organizationCredits && organizationCredits.legacyCredits) ||            personalCredits.legacyCredits          }          colorClass={""}        />      </div>    );  }  return (    <div className={"section-line-flx"}>      <RemainingCreditItem        icon={"work-email-credits.svg"}        credit={          /* organizationCredits.workEmailCredits ||          personalCredits.workEmailCredits*/          credits &&          credits.organizationCredits &&          credits.organizationCredits.subscription ? (            <img              width={23}              src={window.chrome.runtime.getURL(                "/assets/images/infinity-solid.svg"              )}            />          ) : /*<i className="fas fa-infinity"></i>*/          credits &&            credits.personalCredits &&            credits.personalCredits.recurrenceCredits &&            credits.personalCredits.recurrenceCredits["workEmailCredits"]              .isUnlimited ? (            <img              width={23}              src={window.chrome.runtime.getURL(                "/assets/images/infinity-solid.svg"              )}            />          ) : (            (organizationCredits && organizationCredits.workEmailCredits) ||            personalCredits.workEmailCredits ||            0          )        }        colorClass={"workemail-color"}      />      <RemainingCreditItem        icon={"phone-credits.svg"}        credit={          (organizationCredits && organizationCredits.phoneCredits) ||          personalCredits.phoneCredits        }        colorClass={"phone-color"}      />      <RemainingCreditItem        icon={"direct-email-credits.svg"}        credit={          (organizationCredits && organizationCredits.personalEmailCredits) ||          personalCredits.personalEmailCredits        }        colorClass={"directemail-color"}      />      <RemainingCreditItem        icon={"export-credits.svg"}        credit={(organizationCredits && organizationCredits.exportCredits) || 0}        colorClass={"export-color"}      />    </div>  );}function RemainingCreditItem({ icon, credit, colorClass }) {  return (    <div className={"credits-line"}>      <img src={window.chrome.runtime.getURL("assets/images/" + icon)} />      <span className={colorClass}>{credit}</span>    </div>  );}