/* global chrome */import React from "react";import ReactDOM from "react-dom";import { FormattedMessage, useIntl } from "react-intl";import "./SynchronizeAccount.css";import useSynchro from "./SynchronizeAccount.hooks";import { REACT_APP_BASE_URL } from "../../../constants";export default function SynchronizeAccount({  onRefuse,  isFirstKasprSynchro,  currentUrl,  userInfos,}) {  const { state, handlers } = useSynchro({    isFirstKasprSynchro,    currentUrl,    userInfos,  });  return ReactDOM.createPortal(    <div className={"plugin-wrapper"}>      <div className={"kaspr-popup sync-pop " + state.show}>        <div className="kp-in">          <div className="kp-body">            <h2>              <FormattedMessage id={"connectKasprWithLinkedin"} />            </h2>            <h5>              <FormattedMessage id={"doYouWantToUseKasprWithLinkedin"} />            </h5>            <span              className="closeIcon"              onClick={() => {                isFirstKasprSynchro                  ? (window.location = REACT_APP_BASE_URL)                  : onRefuse(true);              }}            />            {state.errorMsg ? (              <>                <h3                  style={{                    textAlign: "center",                    margin: 60,                    color: "mediumvioletred",                  }}                >                  {state.errorMsg.error || state.errorMsg.message}                </h3>                {state.errorMsg.error ? (                  <div className="btn-grp">                    <button                      id="sync-refuse"                      className={                        "btn-white " + (state.synchronized ? "dnone" : "")                      }                      onClick={() => {                        isFirstKasprSynchro                          ? (window.location = REACT_APP_BASE_URL)                          : onRefuse(true);                      }}                    >                      <FormattedMessage id={"noIRefuse"} />                    </button>                    <button                      onClick={() =>                        !state.synchronized                          ? handlers.onSubmit({ force: true })                          : false                      }                      id="sync-btn"                      className={"btn sync-btn " + state.synchronized}                    >                      <span>                        <FormattedMessage id={"connectAnyWay"} />                      </span>                      <span>                        <FormattedMessage id={"Syncing"} />                      </span>                      <span>                        <FormattedMessage id={"Synced"} />                      </span>                    </button>                  </div>                ) : (                  <div style={{ display: "flex", justifyContent: "flex-end" }}>                    <button                      onClick={() => {                        isFirstKasprSynchro                          ? (window.location = REACT_APP_BASE_URL)                          : onRefuse(true);                      }}                      id="sync-btn"                      className="btn sync-btn"                      style={{ width: 150 }}                    >                      ok                    </button>                  </div>                )}              </>            ) : (              <>                <div className="logoUser-grid">                  <div className="kasprLogo">                    <img                      className="img-fluid"                      src={window.chrome.runtime.getURL(                        "assets/images/kaspr.png"                      )}                      width={89}                      alt={""}                    />                  </div>                  <span className="plusIcon" />                  <div className="userPhoto">                    <img                      className="img-fluid"                      src={state.picture}                      alt={""}                      style={{ borderRadius: "50%" }}                    />                  </div>                </div>                <div className="btn-grp">                  <button                    id="sync-refuse"                    className={                      "btn-white " + (state.synchronized ? "dnone" : "")                    }                    onClick={() => {                      isFirstKasprSynchro                        ? (window.location = REACT_APP_BASE_URL)                        : onRefuse(true);                    }}                  >                    <FormattedMessage id={"noIRefuse"} />                  </button>                  <button                    onClick={() =>                      !state.synchronized                        ? handlers.onSubmit({ force: false })                        : false                    }                    id="sync-btn"                    className={"btn sync-btn " + state.synchronized}                  >                    <span>                      <FormattedMessage id={"yesConnectWithLinkedin"} />                    </span>                    <span>                      <FormattedMessage id={"Syncing"} />                    </span>                    <span>                      <FormattedMessage id={"Synced"} />                    </span>                  </button>                </div>                <p className={"terms"}>                  <span>i</span>{" "}                  <FormattedMessage                    id={"termsAndConditions"}                    values={{                      terms: (                        <a href="https://kaspr.io/terms" target={"_blank"}>                          <FormattedMessage id={"terms"} />                        </a>                      ),                      policy: (                        <a                          href="https://kaspr.io/privacy-policy/"                          target={"_blank"}                        >                          <FormattedMessage id={"policy"} />                        </a>                      ),                    }}                  />                </p>              </>            )}          </div>        </div>      </div>    </div>,    document.body  );}