import React, { useEffect, useState } from "react";import "./ModifyLead.css";import { getDataEvent } from "../../../../../services/events";import SelectWithOptions from "./SelectWithOptions/SelectWithOptions";import EditCompany from "./EditCompany/EditCompany";import { FormattedMessage, useIntl } from "react-intl";export default function ModifyLead({ lead, setShowModifyLead }) {  const [leadState, setLeadState] = useState(lead.lead);  const [companies, setCompanies] = useState([]);  const [errorMessage, setErrorMessage] = useState([]);  const [companyInfoState, setCompanyInfoState] = useState(    leadState.companyInfo  );  const [selectedAddress, setSelectedAddress] = useState();  const intl = useIntl();  useEffect(() => {    if (companyInfoState && companyInfoState.mainAddress) {      setSelectedAddress(companyInfoState.mainAddress);    } else {      setSelectedAddress({        country: "",        city: "",        headquarter: null,        postalCode: null,        description: null,        line1: null,      });    }  }, [companyInfoState]);  useEffect(() => {    const domains = leadState.currentProEmails.map((email) => {      if (email) {        return email.split("@")[1];      }      return email;    });    getDataEvent("GET_COMPANIES", { domains }).then(({ data }) => {      setCompanies(data);    });    /*    setCompanies([{domain:'kaspr.io',companyInfos : {name:'kaspr'}}])*/  }, []);  const onSubmit = async () => {    leadState.companyInfo = companyInfoState;    leadState.companyInfo.mainAddress = selectedAddress;    const { data, statusCode } = await getDataEvent("UPDATE_LEAD_INFOS", {      lead: leadState,    });    if (statusCode == 200) {      setShowModifyLead();    } else {      setErrorMessage(data.message);    }  };  const onChange = (e) => {    const leadsToModify = Object.assign({}, leadState);    leadsToModify[e.target.id] = e.target.value;    setLeadState(leadsToModify);  };  const onSelectItem = (id, value) => {    const leadsToModify = Object.assign({}, leadState);    leadsToModify[id] = value;    setLeadState(leadsToModify);  };  const onEditItems = ({ items, type, editedItem, editedItemType }) => {    const leadsToModify = Object.assign({}, leadState);    leadsToModify[type] = items;    if (editedItem) {      leadsToModify[editedItemType] = editedItem;    }    if (editedItem == "delete") {      leadsToModify[editedItemType] = items.length > 0 ? items[0] : "--";    }    setLeadState(leadsToModify);  };  const onCompanyEditItems = (items, type) => {    const companyToModify = Object.assign({}, companyInfoState);    companyToModify[type] = items;    setCompanyInfoState(companyToModify);  };  const onAddressFieldChange = (e) => {    const addressToModify = Object.assign({}, selectedAddress);    addressToModify[e.target.id] = e.target.value;    setSelectedAddress(addressToModify);  };  const onCompanyChange = (e) => {    const companyToModify = Object.assign({}, companyInfoState);    companyToModify[e.target.id] = e.target.value;    setCompanyInfoState(companyToModify);  };  return (    <div className={"modify-lead-form-container"}>      <from className={"modify-lead"}>        <div className={"modify-lead-inline"}>          <div className="form-in">            <label htmlFor="firstName">              <FormattedMessage id="firstName" />            </label>            <input              id="firstName"              type="text"              placeholder={intl.formatMessage({ id: "firstName" })}              className={"custom-btn"}              value={leadState.firstName}              onChange={onChange}            />          </div>          <div className="form-in">            <label htmlFor="lastName">              <FormattedMessage id="lastName" />            </label>            <input              id="lastName"              type="text"              placeholder={intl.formatMessage({ id: "lastName" })}              className={"custom-btn"}              value={leadState.lastName}              onChange={onChange}            />          </div>        </div>        <div className="form-in">          <label htmlFor="job">            <FormattedMessage id="job" />          </label>          <input            id="job"            type="text"            placeholder={intl.formatMessage({ id: "job" })}            className={"custom-btn"}            value={leadState.job}            onChange={onChange}          />        </div>        <div className="form-in">          <label htmlFor="location">            <FormattedMessage id="location" />          </label>          <input            id="location"            type="text"            placeholder={intl.formatMessage({ id: "location" })}            className={"custom-btn"}            value={leadState.location}            onChange={onChange}          />        </div>        <div className="form-in">          <label htmlFor="description">Description</label>          <textarea            id="description"            placeholder={"Description"}            className={"custom-btn"}            value={leadState.description}            onChange={onChange}            style={{ minHeight: 66 }}          />        </div>        <div className="form-in">          <label htmlFor="comment">            <FormattedMessage id="comments" />          </label>          <textarea            id="comment"            placeholder={intl.formatMessage({ id: "comments" })}            className={"custom-btn"}            value={leadState.comment}            onChange={onChange}            style={{ minHeight: 66 }}          />        </div>        <div>          {/*<label htmlFor="currentProEmail">Professional Email</label>        <input id="currentProEmail" type="text" placeholder={"Company"} />*/}          <SelectWithOptions            items={leadState.currentProEmails}            title={intl.formatMessage({ id: "professionalEmail" })}            value={leadState.currentProEmail || "--"}            onSelectItem={(value) => onSelectItem("currentProEmail", value)}            setNewItems={({ values, value }) =>              onEditItems({                items: values,                type: "currentProEmails",                editedItem: value,                editedItemType: "currentProEmail",              })            } /* onEditItems(values, "currentProEmails")}*/            type={"email"}          />          <SelectWithOptions            items={leadState.currentPersonalEmails}            title={intl.formatMessage({ id: "personalEmail" })}            value={leadState.currentPersonalEmail || "--"}            type={"email"}            onSelectItem={(value) =>              onSelectItem("currentPersonalEmail", value)            }            setNewItems={({ values, value }) =>              //   onEditItems(values, "currentPersonalEmails");              onEditItems({                items: values,                type: "currentPersonalEmails",                editedItem: value,                editedItemType: "currentPersonalEmail",              })            }          />          <SelectWithOptions            items={leadState.phones}            title={intl.formatMessage({ id: "Phone" })}            value={leadState.phone || "--"}            onSelectItem={(value) => onSelectItem("phone", value)}            //  setNewItems={(values) => onEditItems(values, "phones")}            setNewItems={({ values, value }) =>              //   onEditItems(values, "currentPersonalEmails");              onEditItems({                items: values,                type: "phones",                editedItem: value,                editedItemType: "phone",              })            }            type={"phone"}          />        </div>        {/*        <div className={"modify-lead-shadow"}></div>*/}        <EditCompany          title={intl.formatMessage({ id: "selectACompany" })}          items={companies}          companyInfo={companyInfoState}          onFieldChange={onCompanyChange}          onItemsChange={onCompanyEditItems}          onAddressFieldChange={onAddressFieldChange}          selectedAddress={selectedAddress}          setSelectedAddress={setSelectedAddress}          setCompanyInfo={setCompanyInfoState}        />      </from>      <div style={{ textAlign: "center", color: "red" }}> {errorMessage} </div>      <button className={"btn modify-lead-save-btn"} onClick={onSubmit}>        <FormattedMessage id={"save"} />      </button>    </div>  );}