import React, { useEffect, useState } from "react";import allIntegrations from "./allIntegrations";import "./IntegrationList.css";import {  addLeadsIntegration,  getAccounts,  getIntegrationsLists,} from "../../../../../services/api";import { getDataEvent } from "../../../../../services/events";import {  capitalizeFirstLetter,  checkPermission,} from "../../../../../services/utils";import { FormattedMessage, useIntl } from "react-intl";import { usePermissions } from "../../../../Components/Permissions/permissions.hooks";import { Permissions } from "../../../../Components";export default function IntegrationList({  setToggle,  leads,  onSuccess,  memberPermissions,  setPermission,  credits,  error,  setError,}) {  const [integrations, setIntegrations] = useState([]);  const [selectedIntegration, setSelectedIntegration] = useState();  const [integrationList, setIntegrationList] = useState(null);  const [selectedIntegrationList, selectIntegrationList] = useState(null);  const [tokenValue, setTokenValue] = useState(null);  const [checkPlanPermissions, checkMemberPermissions] = usePermissions(    credits &&      credits.organizationCredits &&      credits.organizationCredits.plan &&      credits.organizationCredits.plan.permissions,    memberPermissions,    "Integration"  );  const intl = useIntl();  useEffect(() => {    const loadData = async () => {      const _integrations = allIntegrations.filter(        (intg) => intg.active && intg.name !== "linkedin"      );      const { token } = await getDataEvent("GET_TOKEN");      setTokenValue(token.value);      let { data, statusCode } = await getAccounts(token.value);      if (data && data.accounts)        _integrations.map((intg) => {          intg.isConnected = data.accounts[intg.name];          return intg;        });      setIntegrations(_integrations);    };    loadData();  }, []);  const handleSelectIntegration = async (integrationName) => {    if (      integrationName !== "sendinblue" ||      integrationName !== "lemlist" ||      integrationName !== "salesforce"    ) {      setIntegrationList(null);    }    switch (integrationName) {      case "sendinblue":        const { data: sendinBlueData, statusCode: sendinblueStatus } =          await getIntegrationsLists({            integrationName,            token: tokenValue,          });        if (sendinblueStatus == 200) {          // setIntegrationList(sendinBlueData);          setIntegrationList({            name: integrationName,            lists: sendinBlueData.lists,          });        }        selectIntegrationList(null);        break;      case "zapier":        const { data: zapierData, statusCode: zapierStatus } =          await getIntegrationsLists({            integrationName,            token: tokenValue,          });        if (zapierStatus === 200) {          zapierData.lists.push({ name: "Export to all zaps" });          setIntegrationList({            name: integrationName,            lists: zapierData.lists,          });        } else {          setError(zapierData.messageTranslated || zapierData.message);          selectIntegrationList(null);        }        break;      case "lemlist":        const { data: lemlistData, statusCode: lemlistStatus } =          await getIntegrationsLists({            integrationName,            token: tokenValue,          });        if (lemlistStatus == 200) {          setIntegrationList({            name: integrationName,            lists: lemlistData.lists,          });        }        selectIntegrationList(null);        break;      case "salesforce":        setIntegrationList({          name: integrationName,          lists: [{ name: "lead" }, { name: "contact" }],        });        selectIntegrationList(null);        break;      case "salesforceContact":        setIntegrationList({          name: integrationName,          lists: [{ name: "lead" }, { name: "contact" }],        });        selectIntegrationList(null);        break;      case "SalesforceLead":        setIntegrationList({          name: integrationName,          lists: [{ name: "lead" }, { name: "contact" }],        });        selectIntegrationList(null);        break;    }    setSelectedIntegration(integrationName);  };  const handleExportToIntegration = async (e) => {    e.preventDefault();    if (!selectedIntegration) {      setError("You need to select an integration");      return;    }    if (!memberPermissions) {      setToggle(false);      setTimeout(() => {        setPermission(          "You must create an organization to be able to use this feature !"        );        setTimeout(() => {          setPermission(false);        }, 6000);      }, 500);      return;    }    if (!checkMemberPermissions("Export")) {      setToggle(false);      setTimeout(() => {        setPermission(          "You dont have access to this feature! Please contact your admin to give you access !"        );        setTimeout(() => {          setPermission(false);        }, 6000);      }, 500);      return;    }    if (      selectedIntegration == "salesforce"        ? !(            checkPlanPermissions(capitalizeFirstLetter("salesforceContact")) ||            checkPlanPermissions(capitalizeFirstLetter("SalesforceLead"))          )        : selectedIntegration === "zohocrm"        ? !checkPlanPermissions("Zoho-crm")        : !checkPlanPermissions(capitalizeFirstLetter(selectedIntegration))    ) {      setToggle(false);      setTimeout(() => {        setPermission(          "You have to upgrade your plan to be able to use this feature"        );        setTimeout(() => {          setPermission(false);        }, 6000);      }, 500);      return;    }    if (      ["sendinblue", "lemlist", "salesforce"].indexOf(selectedIntegration) >        -1 &&      !selectedIntegrationList    ) {      setError("You need to select a list for " + selectedIntegration);      return;    }    if (selectedIntegration === "zapier" && !selectedIntegrationList) {      setError(intl.formatMessage({ id: "youNeedToSelectOneZap" }));      return;    }    let { statusCode, data } = await addLeadsIntegration({      leads,      integrationName: selectedIntegration,      listIds:        selectedIntegration === "zapier" &&        selectedIntegrationList === "Export to all zaps"          ? []          : [selectedIntegrationList],      token: tokenValue,    });    if (statusCode === 200) {      onSuccess("success");      setTimeout(() => {        onSuccess(false);      }, 4000);      setToggle(false);      setError(null);    } else {      onSuccess("error");      setTimeout(() => {        onSuccess(false);      }, 4000);      onSuccess(false);      setError(        "An error has occurred while lead's export: " +          (data.messageTranslated || data.message)      );    }  };  return (    <div className="select-integration">      <ul>        {integrations.map((integration, i) => (          <List            key={i}            name={integration.name}            image={integration.name + "-icon.png"}            isConnected={integration.isConnected}            onSelect={(name) =>              integration.isConnected && handleSelectIntegration(name)            }            selectedIntegration={selectedIntegration}            integrationList={integrationList}            selectList={selectIntegrationList}            checkPlanPermissions={checkPlanPermissions}            selectedList={selectedIntegrationList}          />        ))}      </ul>      <div>        {!checkMemberPermissions("Export") ? (          <Permissions.CallToAction            isButton={true}            buttonClassName={"btn"}            buttonStyle={{ padding: 6 }}            toolTipStyle={{ width: "100%" }}          >            <FormattedMessage id={"export"} />          </Permissions.CallToAction>        ) : (          <button            className={"btn"}            onClick={              selectedIntegration ? handleExportToIntegration : () => false            }            style={              !selectedIntegration                ? { padding: "6px", background: "#ccc", border: "none" }                : { padding: "6px" }            }          >            <FormattedMessage id={"export"} />          </button>        )}      </div>      {error && (        <p          style={{            textAlign: "center",            color: "red",            margin: "20px 0",            fontWeight: 600,          }}        >          {error} !{" "}        </p>      )}    </div>  );}function List({  name,  image,  onSelect,  isConnected,  selectedIntegration,  integrationList,  selectList,  checkPlanPermissions,  selectedList,}) {  const intl = useIntl();  if (    name == "salesforce"      ? !(          checkPlanPermissions(capitalizeFirstLetter("salesforceContact")) ||          checkPlanPermissions(capitalizeFirstLetter("SalesforceLead"))        )      : name === "zohocrm"      ? !checkPlanPermissions("Zoho-crm")      : !checkPlanPermissions(capitalizeFirstLetter(name))  ) {    return (      <li>        <Permissions.CallToAction          withoutIcon={true}          isPlan={true}          toolTipStyle={{ width: "100%" }}        >          <div className="radio-btn" style={{ margin: "20px 0" }}>            <input              type="radio"              name="selectintegration"              checked={selectedIntegration == name}              disabled={isConnected ? false : true}              readOnly={true}              style={{ background: "transparent" }}            />            <span className="radio-icon"></span>            <div className="radio-text">              <div className="img-block">                <img                  src={window.chrome.runtime.getURL("assets/images/" + image)}                  alt=""                />              </div>              <div className="content-block">                <p>{name}</p>                {isConnected ? (                  <span className="connected-tag">                    <FormattedMessage id={"connected"} />                  </span>                ) : (                  <span className="not-connected-tag">                    <FormattedMessage id={"notConnected"} />                  </span>                )}              </div>            </div>          </div>        </Permissions.CallToAction>      </li>    );  }  return (    <>      <li onClick={isConnected ? () => onSelect(name) : () => {}}>        <div className="radio-btn" style={{ margin: "20px 0" }}>          <input            type="radio"            name="selectintegration"            checked={selectedIntegration == name}            disabled={isConnected ? false : true}            readOnly={true}            style={{ background: "transparent" }}          />          <span className="radio-icon"></span>          <div className="radio-text">            <div className="img-block">              <img                src={window.chrome.runtime.getURL("assets/images/" + image)}                alt=""              />            </div>            <div className="content-block">              <p>{name === "zohocrm" ? "Zoho" : name}</p>              {isConnected ? (                <span className="connected-tag">                  <FormattedMessage id={"connected"} />                </span>              ) : (                <span className="not-connected-tag">                  <FormattedMessage id={"notConnected"} />                </span>              )}            </div>          </div>        </div>      </li>      {integrationList && integrationList.name == name && (        <div className={"custom-integration-list"}>          <select id="" onChange={(e) => selectList(e.target.value)}>            {!selectedList && (              <>                {integrationList.name == "lemlist" ? (                  <option>                    {" "}                    {intl.formatMessage({ id: "selectCampaign" })}                  </option>                ) : /*  : integrationList.name === "salesforce" ? (              <option> {intl.formatMessage({ id: "selectSObject" })}</option>            ) */                integrationList.name === "zapier" ? (                  <option>                    {" "}                    {intl.formatMessage({ id: "extension.selectZap" })}                  </option>                ) : (                  <option> {intl.formatMessage({ id: "selectList" })} </option>                )}              </>            )}            {integrationList.lists.map((list) => (              <option value={list.id}>{list.name}</option>            ))}          </select>        </div>      )}    </>  );}