import { FaCog, FaLock } from "react-icons/fa";import { FormattedMessage } from "react-intl";import { REACT_APP_BASE_URL } from "../../../../constants";import React from "react";export default function HiddenPersonalEmails({ hiddenPersonalEmailsNumber }) {  return (    <div      style={{        marginTop: "8px",        backgroundColor: "#f3f3f4",        borderRadius: "5px",        padding: "5px 5px 7px 10px",        display: "flex",        justifyContent: "space-between",      }}    >      <div>        <FaLock className={"fa-1x"} color={"c03c63"} fontSize={10} />        <span          style={{ fontSize: "13px", fontWeight: "500", marginLeft: "5px" }}        >          {hiddenPersonalEmailsNumber}{" "}          <FormattedMessage id={"hiddenPersonalEmails"} />        </span>      </div>      <span        className={"custom-tooltip-container"}        style={{ position: "relative" }}      >        <span          onClick={() =>            window.open(REACT_APP_BASE_URL + "/settings/workspace")          }          style={{ float: "right", cursor: "pointer" }}        >          <FaCog className={"fa-1x"} color={"566DF2"} fontSize={14} />        </span>        <span          className={"custom-tooltip"}          style={{            left: -100,            fontSize: 11,            width: 130,            marginBottom: 10,            top: "inherit",            height: "max-content",          }}        >          <FormattedMessage id={"clickHereToRedirectToConfigureHideEmails"} />        </span>      </span>    </div>  );}