import React from "react";import { FormattedMessage } from "react-intl";import HiddenPersonalEmails from "../Common/HiddenPersonalEmails";export default function ProfileDataNotFound({ serverError, error, noPhones, hiddenPersonalEmailsNumber }) {  if (error) {    return (      <div className="no-data-found">        <div className="ndf-icon">          <img            className=""            src={window.chrome.runtime.getURL("/assets/images/notfound.svg")}          />        </div>        <div className="ndf-text">          <h4> {error} </h4>        </div>      </div>    );  }  if (serverError) {    return (      <div className="no-data-found">        <div className="ndf-icon">          <img            className=""            src={window.chrome.runtime.getURL("/assets/images/notfound.svg")}          />        </div>        <div className="ndf-text">          <h4>            {" "}            <FormattedMessage id={"oupsAnErrorHasOccurred"} />{" "}          </h4>          <h6>            {" "}            <FormattedMessage id={"tryToRefreshThePage"} />{" "}          </h6>        </div>      </div>    );  }  if (noPhones) {    return (      <div className="no-data-found" style={{ padding: 0 }}>        <div className="ndf-text" style={{ marginTop: "145px" }}>          <h4>            <FormattedMessage id={"itAppearsThatWeDontHaveAnyContact"} />            {!!hiddenPersonalEmailsNumber && <HiddenPersonalEmails hiddenPersonalEmailsNumber={hiddenPersonalEmailsNumber}/>}          </h4>        </div>      </div>    );  }  return (    <div className="no-data-found">      <div className="ndf-icon">        <img          className=""          src={window.chrome.runtime.getURL("/assets/images/nodata-found.svg")}          width={140}        />      </div>      <div className="ndf-text">        <h4>          {" "}          <FormattedMessage id={"sorryWeCouldNtFindContactDetails"} />{" "}        </h4>        <h6>          <FormattedMessage            id={"itsEmbarassingWeCouldntFindContactInformation"}          />        </h6>        {!!hiddenPersonalEmailsNumber && <HiddenPersonalEmails hiddenPersonalEmailsNumber={hiddenPersonalEmailsNumber}/>}      </div>    </div>  );}