import React, { useEffect, useRef, useState } from "react";import SelectWithOptions from "../SelectWithOptions/SelectWithOptions";import SelectAddress from "./SelectAddress/SelectAddress";import { useIntl } from "react-intl";export default function EditCompany({  title,  items,  style,  dropDownStyle,  formInStyle,  companyInfo,  setCompanyInfo,  onFieldChange,  onItemsChange,  onAddressFieldChange,  selectedAddress,  setSelectedAddress,}) {  const [toggle, setToggle] = useState(false);  const ref = useRef(null);  const intl = useIntl();  useEffect(() => {    document.addEventListener("mousedown", handleClickOutside);    return () => document.removeEventListener("mousedown", handleClickOutside);  }, []);  function handleClickOutside(e) {    if (ref.current && !ref.current.contains(e.target)) {      setToggle(false);    }  }  const onDropDown = (e) => {    e.preventDefault();    setToggle(!toggle);  };  const handleChooseItem = (item) => {    //  onSelectItem(item);    setCompanyInfo(item);    setTimeout(() => {      setToggle(false);    }, 10);  };  const Item = ({ label }) => {    return (      <div        className="cdi-in"        style={{ cursor: "default" }}        onClick={() => handleChooseItem(label)}      >        <input type="radio" name="List" />        <p          className="mb-0"          style={{            display: "flex",            padding: "0 10px",            justifyContent: "space-between",          }}        >          <span style={{ cursor: "pointer" }}>{label.name}</span>        </p>      </div>    );  };  const Items = () => {    return (      <>        {items.map((label, i) => (          <Item label={label} index={i} />        ))}      </>    );  };  return (    <div className={"company-edit"}>      <div className="form-in" ref={ref} style={formInStyle || {}}>        <label>{title}</label>        <div className={"custom-dropdown workflow " + (toggle ? "show" : "")}>          <button            className="custom-btn"            onClick={onDropDown}            style={style || {}}          >            {(companyInfo && companyInfo.name) || "--"}          </button>          <div            className="custom-dropdown-in drop-down-style "            style={dropDownStyle || {}}          >            <Items />          </div>        </div>      </div>      {companyInfo && (        <div>          <Field            name={intl.formatMessage({ id: "company" })}            id={"name"}            value={companyInfo.name}            onChange={onFieldChange}          />          <Field            name={intl.formatMessage({ id: "foundedOn" })}            id={"foundedOn"}            value={companyInfo.foundedOn}            onChange={onFieldChange}          />          <Field            name={intl.formatMessage({ id: "industryName" })}            id={"industryName"}            value={companyInfo.industryName}            onChange={onFieldChange}          />          <Field            name={intl.formatMessage({ id: "pageUrl" })}            id={"companyPageUrl"}            value={companyInfo.companyPageUrl}            onChange={onFieldChange}          />          <Field            name={intl.formatMessage({ id: "universalName" })}            id={"universalName"}            value={companyInfo.universalName}            onChange={onFieldChange}          />          <SelectWithOptions            items={companyInfo.specialities}            title={intl.formatMessage({ id: "specialities" })}            value={              companyInfo.specialities.length > 0                ? companyInfo.specialities.slice(0, 2).join(", ") + "..."                : intl.formatMessage({ id: "addSpecialities" })            }            onSelectItem={() => false}            setNewItems={({ values }) => onItemsChange(values, "specialities")}          />          {companyInfo.addresses.length > 0 && (            <SelectAddress              items={companyInfo.addresses}              title={intl.formatMessage({ id: "selectAddress" })}              value={                companyInfo.addresses                  .map(                    (address) =>                      (address.line1 || "") +                      " " +                      (address.country || "") +                      " " +                      (address.city || "") +                      "  " +                      (address.postalCode || "")                  )                  .slice(0, 2)                  .join(", ") + "..."              }              onSelectItem={setSelectedAddress}            />          )}          {selectedAddress && (            <div>              <Field                name={intl.formatMessage({ id: "country" })}                id={"country"}                value={selectedAddress.country}                onChange={onAddressFieldChange}              />              <Field                name={intl.formatMessage({ id: "city" })}                id={"city"}                value={selectedAddress.city}                onChange={onAddressFieldChange}              />              <Field                name={"Description"}                id={"description"}                value={selectedAddress.description}                onChange={onAddressFieldChange}              />              <Field                name={intl.formatMessage({ id: "line" })}                id={"line1"}                value={selectedAddress.line1}                onChange={onAddressFieldChange}              />              <Field                name={intl.formatMessage({ id: "postalCode" })}                id={"postalCode"}                value={selectedAddress.postalCode}                onChange={onAddressFieldChange}              />            </div>          )}        </div>      )}    </div>  );}function Field({ name, type, value, onChange, id }) {  return (    <div className="form-in">      <label htmlFor="company">{name}</label>      <input        id={id}        type={type || "text"}        placeholder={name}        className={"custom-btn"}        value={value}        onChange={onChange}      />    </div>  );}