import React, { useEffect, useRef, useState } from "react";export default function SelectAddress({  title,  items,  onSelectItem,  value,  style,  dropDownStyle,  formInStyle,}) {  const [toggle, setToggle] = useState(false);  const ref = useRef(null);  useEffect(() => {    document.addEventListener("mousedown", handleClickOutside);    return () => document.removeEventListener("mousedown", handleClickOutside);  }, []);  function handleClickOutside(e) {    if (ref.current && !ref.current.contains(e.target)) {      setToggle(false);    }  }  const onDropDown = (e) => {    e.preventDefault();    setToggle(!toggle);  };  const handleChooseItem = (item) => {    onSelectItem(item);    setTimeout(() => {      setToggle(false);    }, 10);  };  const Item = ({ label, index }) => {    return (      <div        className="cdi-in"        style={{ cursor: "pointer" }}        onClick={() => handleChooseItem(label)}      >        <input type="radio" name="List" />        <p          className="mb-0"          style={{            display: "flex",            padding: "0 10px",            justifyContent: "space-between",          }}        >          <span            style={{ cursor: "pointer" }}            onClick={() => handleChooseItem(label)}          >            {(label.line1 || "") +              " " +              (label.country || "") +              " " +              (label.city || "") +              "  " +              (label.postalCode || "")}          </span>        </p>      </div>    );  };  const Items = () => {    return (      <>        {items.map((label, i) => (          <Item label={label} index={i} />        ))}      </>    );  };  return (    <div className="form-in" ref={ref} style={formInStyle || {}}>      <label>{title}</label>      <div className={"custom-dropdown workflow " + (toggle ? "show" : "")}>        <button className="custom-btn" onClick={onDropDown} style={style || {}}>          {value}        </button>        <div          className="custom-dropdown-in drop-down-style "          style={dropDownStyle || {}}        >          <Items />        </div>      </div>    </div>  );}