import React, { useEffect, useRef, useState } from "react";import { FormattedMessage, useIntl } from "react-intl";export default function ListDrop({  type,  defaultLabel,  list,  onSelectItem,  value,  style,  className,}) {  const [toggle, setToggle] = useState(false);  const ref = useRef(null);  const intl = useIntl();  useEffect(() => {    document.addEventListener("mousedown", handleClickOutside);    return () => document.removeEventListener("mousedown", handleClickOutside);  }, []);  function handleClickOutside(e) {    if (ref.current && !ref.current.contains(e.target)) {      setToggle(false);    }  }  const onDropDown = (e) => {    e.preventDefault();    setToggle(!toggle);  };  const handleChooseItem = (item) => {    onSelectItem(item);    setTimeout(() => {      setToggle(false);    }, 10);  };  const Item = () => {    if (!list) return <div />;    return (      <>        <div          className="cdi-in"          onClick={() =>            handleChooseItem({              name: intl.formatMessage({ id: "defaultList" }),              id: null,            })          }        >          <input type="radio" name="List" />          <p className="mb-0">            {" "}            <FormattedMessage id={"defaultList"} />          </p>        </div>        {list.map((item) => (          <div className="cdi-in" onClick={() => handleChooseItem(item)}>            <input type="radio" name="List" />            <p className="mb-0">{item.name}</p>          </div>        ))}      </>    );  };  return (    <div className={"custom-dropdown  " + (toggle ? "show" : "")} ref={ref}>      <button className={className} onClick={onDropDown} style={style || {}}>        {defaultLabel}      </button>      <div        className="custom-dropdown-in drop-down-style"        style={{          boxShadow: "0 9px 9px rgb(0, 0, 0,.12)",          width: "max-content",        }}      >        <Item />      </div>    </div>  );}