import { CREDITS, REACT_APP_API_URL, REACT_APP_BASE_URL } from "../constants";import { getDataEvent } from "./events";export const notificationEmitter = (value) => {  const event = new CustomEvent("notification", {    detail: value,  });  document.dispatchEvent(event);};export const emitter = (type, value) => {  const event = new CustomEvent(type, {    detail: value,  });  document.dispatchEvent(event);};export const getJsonDataUserFromHtml = async (link) => {  let htmlPage = await fetch(link || window.location.href);  htmlPage = await htmlPage.text();  let div = document.createElement("div");  div.innerHTML = htmlPage;  let codeTags = div.childNodes;  let dataUser = [];  for (let tag of codeTags) {    if (tag.nodeName == "CODE") {      dataUser.push(tag.innerHTML);    }  }  dataUser = dataUser.filter((data) => data.match("linkedin.com/in"))[0];  dataUser = dataUser    .replace(/&quot;/g, '"')    .replace(/&#61;/g, "=")    .replace(/&amp;/g, "&");  return JSON.parse(dataUser);};export async function convertToJsonAndHandleErrors(res) {  const hasError = !res.ok;  const statusCode = await res.status;  let data = {};  try {    data = await res.json();  } catch (err) {}  if (hasError && data && data.message) {    if (      data.message == "jwt malformed" ||      data.message == "Invalid token" ||      data.message ==        "You do not provide a JSON Web Token in the Authorization header" ||      data.message == "Session is no longer valid"    ) {      await getDataEvent("REMOVE_COOKIE");    }  }  return { data, statusCode };}export const checkPermission = ({ permissions, name, action }) => {  let isAllowed = false;  let isPlanPermissionAllowed = true;  if (permissions && name && action) {    isAllowed = permissions.find((p) => p.name === name);    if (isAllowed) isAllowed = isAllowed.actions.find((a) => a == action);  }  return isAllowed && isPlanPermissionAllowed;};export const checkPlanPermissions = ({ permissions, name }) => {  let isPrevent = false;  if (permissions && name) {    isPrevent = permissions.find((p) => p.name === name);  }  return isPrevent && isPrevent.limited;};export const loopCheck = (check, callBack) => {  let i = 0;  const verify = () => {    if (!check && i < 10) {      setTimeout(() => {        i++;        verify();        callBack(check);      }, 600);    } else {      callBack(check);    }  };  return verify();};export const capitalizeFirstLetter = (string) => {  return string && string.charAt(0).toUpperCase() + string.slice(1);};export const isOnboardingStepValidated = ({ key, steps }) => {  let step = steps.find((step) => step.id == key);  return step && step.done;};export const hasCredits = (remaining) => {  let total = 0;  CREDITS.filter((credit) => credit !== "exportCredits").forEach((credit) => {    total +=      (remaining.personalCredits[credit] || 0) +      (remaining.organizationRemaining[credit] || 0);  });  return total;};