import React, { useEffect, useState } from "react";import Copy from "../../../Components/Copy/Copy";import { FaThumbsDown, FaThumbsUp } from "react-icons/fa";import { rateData, getRatedData } from "../../../../services/api";import { getDataEvent } from "../../../../services/events";import { FormattedMessage } from "react-intl";export default function Rating({ item }) {  const [token, setToken] = useState(null);  const [isPro, setIsPro] = useState(null);  const [isLike, setIsLike] = useState(null);  const buttonLeftStyle = {    width: "50%",    borderRadius: 0,    borderTopLeftRadius: 6,    borderBottomLeftRadius: 6,    border: "none",  };  const buttonRightStyle = {    width: "50%",    borderRadius: 0,    borderTopRightRadius: 6,    borderBottomRightRadius: 6,    border: "none",  };  useEffect(() => {    getDataEvent("GET_TOKEN").then(({ token }) => {      if (!token) return;      setToken(token.value);      getRatedData({ token: token.value, value: item }).then(        ({ data: { rating } }) => {          if ({ data: { rating } }) {            setIsLike(rating.isValid);            setIsPro(rating.isPro);          }        }      );    });  }, []);  const like = () => {    let liked = false;    if (isLike === true) {      liked = null;    } else {      liked = true;    }    setIsLike(liked);    rateData({ token, item, isLike: liked, isPro }).then(      ({ data: { rating } }) => {        if ({ data: { rating } }) {          setIsLike(rating.isValid);          setIsPro(rating.isPro);        }      }    );  };  const dislike = () => {    let liked = true;    if (isLike === false) {      liked = null;    } else {      liked = false;    }    setIsLike(liked);    rateData({ token, item, isLike: liked, isPro }).then(      ({ data: { rating } }) => {        if ({ data: { rating } }) {          setIsLike(rating.isValid);          setIsPro(rating.isPro);        }      }    );  };  const setPro = () => {    let pro = false;    if (isPro === true) {      pro = null;    } else {      pro = true;    }    setIsPro(pro);    rateData({ token, item, isLike, isPro: pro }).then(      ({ data: { rating } }) => {        if ({ data: { rating } }) {          setIsLike(rating.isValid);          setIsPro(rating.isPro);        }      }    );  };  const setPerso = () => {    let pro = true;    if (isPro === false) {      pro = null;    } else {      pro = false;    }    setIsPro(pro);    rateData({ token, item, isLike, isPro: pro }).then(      ({ data: { rating } }) => {        if ({ data: { rating } }) {          setIsLike(rating.isValid);          setIsPro(rating.isPro);        }      }    );  };  return (    <div style={{ display: "inline-block" }}>      <div className="rating" style={{ display: "inline-block" }}>        <span className="like grow" onClick={() => like()}>          <FaThumbsUp className={isLike === true ? "fa-3x active" : "fa-3x"} />        </span>        <span className="dislike grow" onClick={() => dislike()}>          <FaThumbsDown            className={isLike === false ? "fa-3x active" : "fa-3x"}          />        </span>        <div          style={{            marginLeft: "30px",            display: "inline-block",          }}        >          <button            style={              isPro === true                ? {                    backgroundColor: "rgb(136, 123, 235)",                    ...buttonLeftStyle,                  }                : {                    ...buttonLeftStyle,                    backgroundColor: "#bfb9cf",                  }            }            className={"btn xs"}            onClick={setPro}          >            {" "}            <FormattedMessage id={"professional"} />          </button>          <button            style={              isPro === false                ? { backgroundColor: "rgb(136, 123, 235)", ...buttonRightStyle }                : { ...buttonRightStyle, backgroundColor: "#bfb9cf" }            }            className={"btn xs"}            onClick={setPerso}          >            {" "}            <FormattedMessage id={"Personal"} />          </button>        </div>      </div>    </div>  );}