import React, {useState} from 'react';import './Copy.css';export default function Copy({children}) {  const [copied, setCopied] = useState({    display: 'none',    opacity: '1',    textDecoration: 'none'  });  const handleCopy = () => {    navigator.clipboard.writeText(children);    //  span 'Copied' animation    setCopied({});    setTimeout(() => {      setCopied({...copied, opacity: 0});    }, 500);    setTimeout(() => {      setCopied({...copied, display: 'none', opacity: 1});    }, 700);  };  return (    <span onClick={handleCopy} className={'to-clipboard-wrapper'}>      <span className={'to-clipboard'}>{children}</span>      <span style={copied} className={'copied-box'}>        Copied      </span>    </span>  );}