import { useEffect, useState } from "react";import { getDataEvent } from "../../../services/events";import { emitter, isOnboardingStepValidated } from "../../../services/utils";import { CREDITS, CREDITS_MATCH } from "../../../constants";/** * PROFILE NAME & PICTURE * @param currentUrl * @returns {{previousId: *, name: *, id: *, picture: *}} */export function useProfileInfos(  currentUrl,  isPluginTop,  linkedinClasses,  isVersion2) {  const [name, setName] = useState(null);  const [picture, setPicture] = useState(null);  const [id, setId] = useState(null);  const [previousId, setPreviousId] = useState(id);  const [firstTutorialRun, setFirstTutorialRun] = useState(id);  const [source, setSource] = useState(null);  useEffect(() => {    getDataEvent("GET_TUTORIAL_DATA").then((data) => {      if (data) {        const stateFromCookies = JSON.parse(data);        setFirstTutorialRun(stateFromCookies.firstTutorialRun);      }    });  }, []);  // Sales Navigator  const getSalesNavData = () => {    let urlSplitted = currentUrl.split("/");    let currentId =      urlSplitted.indexOf("people") > -1        ? urlSplitted[urlSplitted.indexOf("people") + 1]        : urlSplitted[urlSplitted.indexOf("lead") + 1];    setPreviousId(id);    setId(currentId);    let [currentName, currentPicture] = getNameAndPicture({      nameClass: linkedinClasses.salesNavigator.name,      pictureClass: linkedinClasses.salesNavigator.picture,    });    const verification =      currentName &&      currentName !== name &&      currentPicture &&      currentPicture !== picture &&      id !== currentId;    if (verification) {      setName(currentName);      setPicture(currentPicture);    } else {      setTimeout(() => {        if (verification) {          let [currentName, currentPicture] = getNameAndPicture({            nameClass: linkedinClasses.salesNavigator.name,            pictureClass: linkedinClasses.salesNavigator.picture,          });          if (!currentName) {            setName("--");            return setPicture(currentPicture || "--");          }          setName(currentName);          setPicture(currentPicture);        } else {          setTimeout(() => {            let [currentName, currentPicture] = getNameAndPicture({              nameClass: linkedinClasses.salesNavigator.name,              pictureClass: linkedinClasses.salesNavigator.picture,            });            if (!currentName) {              setName("--");              return setPicture(currentPicture || "--");            }            setName(currentName);            setPicture(currentPicture);          }, 2000);        }      }, 1000);    }  };  // Linkedin  const getProfileInfos = ({ doc, linkedinClass, isPicture }) => {    let info = doc      ? doc.querySelector(linkedinClass)      : document.querySelector(linkedinClass);    if (isPicture) return info ? info.src : null;    return info ? info.innerText.trim() : null;  };  const getNameAndPicture = ({ nameClass, pictureClass }) => {    return [      getProfileInfos({        linkedinClass: nameClass || ".break-words",      }),      getProfileInfos({        linkedinClass: pictureClass || ".pv-top-card__photo",        isPicture: true,      }),    ];  };  const getLinkedinData = () => {    let urlSplitted = currentUrl.split("/");    let currentId = urlSplitted[urlSplitted.indexOf("in") + 1];    setPreviousId(id);    setId(currentId);    const [currentName, currentPicture] = getNameAndPicture({      nameClass: linkedinClasses.linkedin.name,      pictureClass: linkedinClasses.linkedin.picture,    });    if (      currentName &&      currentName !== name &&      currentPicture &&      currentPicture !== picture &&      id !== currentId    ) {      setName(currentName);      setPicture(currentPicture);    } else {      setTimeout(() => {        const [currentName, currentPicture] = getNameAndPicture({          nameClass: linkedinClasses.linkedin.name,          pictureClass: linkedinClasses.linkedin.picture,        });        setName(currentName);        setPicture(currentPicture);      }, 600);    }  };  function getLinkedinDataFromPlugin(tab) {    window.chrome.tabs.executeScript(      tab.id,      {        code: "(()=> document.body.innerHTML)()",      },      (linkedinDom) => {        let doc = document.createElement("div");        doc.innerHTML = linkedinDom;        let _name, _picture, urlSplitted, _id;        if (tab.url.indexOf("linkedin.com/in/") > -1) {          setSource("Linkedin");          _name = getProfileInfos({            doc,            linkedinClass: linkedinClasses.linkedin.name,          });          _picture = getProfileInfos({            doc,            linkedinClass: linkedinClasses.linkedin.picutre,            isPicture: true,          });          urlSplitted = tab.url.split("/");          _id = urlSplitted[urlSplitted.indexOf("in") + 1];        }        if (tab.url.indexOf("linkedin.com/talent/") > -1) {          setSource("Recruiter");          _name = getProfileInfos({            doc,            linkedinClass: linkedinClasses.recruiter.name,          });          _picture = getProfileInfos({            doc,            linkedinClass: linkedinClasses.recruiter.picture,            isPicture: true,          });          let idElement = doc.querySelector(".public-profile.searchable a");          if (idElement) {            let idLink = idElement.getAttribute("href");            urlSplitted = idLink.split("/");            _id = urlSplitted[urlSplitted.indexOf("in") + 1];          } else {            setTimeout(() => {              let idLink = idElement.getAttribute("href");              let urlSplitted = idLink.split("/");              _id = urlSplitted[urlSplitted.indexOf("in") + 1];            }, 500);          }        }        if (tab.url.indexOf("linkedin.com/sales/") > -1) {          setSource("SalesNavigator");          urlSplitted = tab.url.split("/");          _id =            urlSplitted.indexOf("people") > -1              ? urlSplitted[urlSplitted.indexOf("people") + 1]              : urlSplitted[urlSplitted.indexOf("lead") + 1];          _name = getProfileInfos({            doc,            linkedinClass: linkedinClasses.salesNavigator.name,          });          _picture = getProfileInfos({            doc,            linkedinClass: linkedinClasses.salesNavigator.picture,            isPicture: true,          });          if (!_name && _id) {            setId(_id);            setName("--");            return setPicture(_picture || "--");          }        }        setId(_id);        setName(_name);        setPicture(_picture);      }    );  }  const getRecruiterData = () => {    let idElement = document.querySelector(".personal-info__link");    let currentId = null;    if (idElement) {      let idLink = idElement.getAttribute("href");      let urlSplitted = idLink.split("/");      currentId = urlSplitted[urlSplitted.indexOf("in") + 1];      setPreviousId(id);      setId(currentId);    } else {      setTimeout(() => {        idElement = document.querySelector(".personal-info__link");        let idLink = idElement.getAttribute("href");        let urlSplitted = idLink.split("/");        currentId = urlSplitted[urlSplitted.indexOf("in") + 1];        setPreviousId(id);        setId(currentId);      }, 2000);    }    const [currentName, currentPicture] = getNameAndPicture({      nameClass: linkedinClasses.recruiter.name,      pictureClass: linkedinClasses.recruiter.picture,    });    if (      currentName &&      currentName !== name &&      currentPicture &&      currentPicture !== picture &&      id !== currentId    ) {      setName(currentName);      setPicture(currentPicture);    } else {      setTimeout(() => {        const [currentName, currentPicture] = getNameAndPicture({          nameClass: linkedinClasses.recruiter.name,          pictureClass: linkedinClasses.recruiter.picture,        });        setName(currentName);        setPicture(currentPicture);      }, 2000);    }  };  useEffect(() => {    if (isPluginTop) {      window.chrome.tabs.getSelected(null, (tab) => {        getLinkedinDataFromPlugin(tab);      });    } else {      if (        currentUrl.indexOf("linkedin.com/sales/people/") > -1 ||        currentUrl.indexOf("linkedin.com/sales/lead/") > -1      ) {        getSalesNavData();        setSource("SalesNavigator");      } else if (currentUrl.indexOf("www.linkedin.com/talent") > -1) {        setSource("Recruiter");        getRecruiterData();      } else {        setSource("Linkedin");        getLinkedinData();      }    }    return () => {      setName(null);      setPicture(null);      setId(null);    };  }, [currentUrl]);  return { name, picture, id, previousId, firstTutorialRun, source };}/** *  PROFILE EMAILS & PHONES * @param id * @param previousId * @param name * @returns {{handler: {onDiscover: *}, state: {data: *, errorMessage: *, loading: *}}} */export function useProfileData({  id,  previousId,  name,  userInfos,  updateRemainingCredits,  picture,  source,  organization,  isVersion2,}) {  const [data, setData] = useState(null);  const [errorMessage, setErrorMessage] = useState(null);  const [loading, setLoading] = useState(false);  const [leadsLabels, setLeadsLabels] = useState(null);  const [addLeadErrorMessage, setAddLeadErrorMessage] = useState(null);  const [linkedinPublicId, setLinkedinPublicId] = useState(null);  const [discoverLoading, setDiscoverLoading] = useState({    advancedSearch: false,    phoneCredits: false,    personalEmailCredits: false,    exportCredits: false,    workEmailCredits: false,    legacyCredits: false,  });  const [hiddenPersonalEmailsNumber, setHiddenPersonalEmailsNumber] =    useState(null);  useEffect(() => {    setLoading(true);    emitter("loading", true);    if (id !== previousId) {      emitter("notification", null);      setData(null);      setErrorMessage(null);    }    if (      (id && id !== previousId && name) ||      (document.referrer &&        document.referrer.indexOf("/in/" + previousId) === -1) // fix open profile on new tab    ) {      if (!(id && name)) return;      getDataEvent("GET_HIDDEN_DATA_PROFILE", {        id,        name,        pictureUrl: picture,        source,      }).then(({ data: dataExplored, statusCode }) => {        if (          dataExplored.profile &&          dataExplored.profile.linkedin &&          dataExplored.profile.linkedin.id        ) {          setLinkedinPublicId(dataExplored.profile.linkedin.id);        }        emitter("notification", null);        if (statusCode == 200) {          if (dataExplored.profile.phones.length === 0) {            dataExplored.showAdvancedSearch = true;          }          setHiddenPersonalEmailsNumber(            dataExplored.profile &&              dataExplored.profile.hiddenPersonalEmailsNumber          );          emitter(            "notification",            dataExplored.profile.emails.length +              dataExplored.profile.phones.length          );          setData(dataExplored);        } else {          setErrorMessage(dataExplored.message);          setHiddenPersonalEmailsNumber(            dataExplored.hiddenPersonalEmailsNumber          );          emitter("notification", null);        }        setLoading(false);        emitter("loading", false);      });      getDataEvent("GET_LEADS_LABELS").then(({ data: dataLabels }) => {        setLeadsLabels(dataLabels.labels);      });    } else {      setLoading(false);      emitter("loading", false);    }    return () => {      // setErrorMessage(null);      // setLoading(false);      if (id === !previousId) setData(null);      setLinkedinPublicId(null);    };  }, [id, name]);  const formatLoading = (types, status) => {    return types.reduce(      (previous, credit) => ({ ...previous, [credit]: status }),      {}    );  };  const onDiscover = (types, isAdvancedSearch) => {    setData({ ...data, loading: true });    if (isAdvancedSearch) {      setDiscoverLoading({ advancedSearch: true });    } else {      setDiscoverLoading({        advancedSearch: false,        ...formatLoading(types, true),      });    }    const isFirstDiscover =      !isOnboardingStepValidated({        key: "discover",        steps: userInfos.steps,      }) && !userInfos.denyFreeCredits;    getDataEvent("GET_DATA_PROFILE", {      id: linkedinPublicId || id,      name,      source,      types: types.map((type) => CREDITS_MATCH[type]),      isFirstDiscover,    }).then(async ({ data: discoveredData, statusCode }) => {      setDiscoverLoading({        advancedSearch: false,        ...formatLoading(types, false),      });      if (statusCode == 200) {        if (userInfos) {          discoveredData.isFirstDiscover = isFirstDiscover;        }        discoveredData.loading = false;        if (data && data.profile) {          setHiddenPersonalEmailsNumber(            data.profile.hiddenPersonalEmailsNumber          );        }        if (          data &&          data.showAdvancedSearch &&          discoveredData.profile.phones.length == 0 &&          types.indexOf("phoneCredits") > -1        ) {          discoveredData.noPhonesFoundByAdvancedSearch = true;        } else {          if (!discoveredData.profile.phones.length)            discoveredData.showAdvancedSearch = true;        }        if (userInfos.autoSaveLead && !discoveredData.lead._id) {          const { data: leadAddedData, statusCode: leadAddedStatusCode } =            await getDataEvent("ADD_LEAD", {              leads: [discoveredData.profile],            });          if (leadAddedStatusCode == 200) {            discoveredData.lead = {              ...discoveredData.lead,              ...leadAddedData.leads[0],            };            discoveredData.lead.isLeadAdded = true;          }        } else {          if (discoveredData.lead._id) discoveredData.lead.isLeadAdded = true;        }        if (discoveredData && discoveredData.lead._id) {          discoveredData.profile._id = discoveredData.lead._id;          const { data: leadUpdated, statusCode: leadUpdatedStatus } =            await getDataEvent("UPDATE_LEAD_INFOS", {              lead: discoveredData.profile,            });          if (leadUpdatedStatus == 200) {            discoveredData.lead = {              ...discoveredData.lead,              ...leadUpdated.lead,            };          }        }        discoveredData.lead.isFirstDiscover = true;        discoveredData.loading = false;        setData(discoveredData);        updateRemainingCredits(discoveredData.remainingCredits);        setErrorMessage(null);      } else {        //Ancient check for waiting list        if (          discoveredData.message === "No Phone Numbers Found" ||          discoveredData.message === "No Email Found"        ) {          discoveredData.noPhones = true;          setData(discoveredData);          return setErrorMessage("No Email Found");        }        if (statusCode == 500) {          setErrorMessage("server error");        } else {          setErrorMessage(discoveredData.message);        }        setData({ ...discoveredData, loading: false });      }      setLoading(false);      emitter("loading", false);    });  };  const addToLeads = async (leads) => {    setAddLeadErrorMessage(null);    const isFirstLeadAdded =      !isOnboardingStepValidated({        key: "leadSaved",        steps: userInfos.steps,      }) && !userInfos.denyFreeCredits;    if (data.isFirstDiscover) onDeleteDataKeys(["isFirstDiscover"]);    const { data: leadAddedData, statusCode: leadAddedStatusCode } =      await getDataEvent("ADD_LEAD", {        leads,        isFirstLeadAdded,      });    if (leadAddedStatusCode == 200) {      const newData = Object.assign({}, data);      newData.lead.isLeadAdded = true;      newData.lead = { ...newData.lead, ...leadAddedData.leads[0] };      if (userInfos) {        leadAddedData.isFirstLeadAdded = isFirstLeadAdded;      }      setData(newData);    } else {      setAddLeadErrorMessage(leadAddedData.messageTranslated);    }  };  const addLeadToList = async (label, dataProfile) => {    const { statusCode } = await getDataEvent("UPDATE_LEAD", {      leads: [dataProfile.lead],      labelId: label.id,    });    const newData = Object.assign({}, data);    newData.lead.label = label.id;    newData.lead.statusCode = statusCode;    setData(newData);  };  const onDeleteLead = async (leadId) => {    await getDataEvent("DELETE_LEAD", { ids: [leadId] });    const newData = Object.assign({}, data);    newData.lead.isLeadAdded = false;    newData.lead.labelId = null;    setData(newData);  };  const onAddPreLeadToWaitingList = async () => {    let existingData = Object.assign({}, data);    existingData.loading = true;    existingData.leadAddedSatusCode = false;    setData(existingData);    const { data: preLeadAddedData, statusCode: leadAddedSatusCode } =      await getDataEvent("ADD_PRELEAD_TO_WAITING_LIST", {        linkedinId: id,        name,      });    preLeadAddedData.loading = false;    if (leadAddedSatusCode == 200) {      preLeadAddedData.isPreLeadAdded = true;    }    setData(preLeadAddedData);    setErrorMessage("No Email Found");  };  function onDeleteDataKeys(types) {    let dataObj = { ...data };    for (let type of types) dataObj[type] = false;    setData(dataObj);  }  return {    state: {      data,      errorMessage,      loading,      leadsLabels,      addLeadErrorMessage,      hiddenPersonalEmailsNumber,      discoverLoading,    },    handler: {      onDiscover,      // onAdvanceSearch,      onAddPreLeadToWaitingList,      addLeadToList,      addToLeads,      onDeleteLead,      onDeleteDataKeys,    },  };}