let REACT_APP_BASE_URL = "https://app.kaspr.io";let REACT_APP_API_URL = "https://api.kaspr.io/";let IS_PROD = true;if (process.env.REACT_APP_ENVIRONMENT == "preprod") {  REACT_APP_BASE_URL = "https://staging.kaspr.io";  REACT_APP_API_URL = "https://staging.api.kaspr.io/";  IS_PROD = false;}if (process.env.REACT_APP_ENVIRONMENT == "local") {  REACT_APP_BASE_URL = "http://localhost:3000";  REACT_APP_API_URL = "http://localhost:8000/";  IS_PROD = false;}if (process.env.REACT_APP_ENVIRONMENT == "prod") {  REACT_APP_BASE_URL = "https://app.kaspr.io";  REACT_APP_API_URL = "https://api.kaspr.io/";  IS_PROD = true;}let CREDITS = [  "phoneCredits",  "personalEmailCredits",  "exportCredits",  "workEmailCredits",  "legacyCredits"];let CREDITS_MATCH = {  phoneCredits: "phone",  personalEmailCredits: "personalEmail",  workEmailCredits: "workEmail",};let DATA_MATCH = {  phoneCredits: "phones",  personalEmailCredits: "currentPersonalEmails",  workEmailCredits: "currentProEmails",};const headers = {  Accept: "application/json, text/plain, */*",  "Content-Type": "application/json",};export {  REACT_APP_API_URL,  REACT_APP_BASE_URL,  headers,  IS_PROD,  CREDITS,  CREDITS_MATCH,  DATA_MATCH};