import React, { useState } from "react";import { FormattedMessage } from "react-intl";export default function CompanyBox({ title, data, type, discovered }) {  const [showAll, setShowAll] = useState(false);  if (!data) return <div />;  const Phones = () => {    if (data.length <= 2) {      return data.map((item) => <p className="user-info">{item}</p>);    } else {      if (showAll) {        return (          <div>            {data.map((item) => (              <p className="user-info">{item}</p>            ))}            <button className="more-info" onClick={() => setShowAll(!showAll)}>              - <FormattedMessage id={"hide"} />{" "}              <span style={{ position: "relative", top: 2 }}>^</span>            </button>          </div>        );      }      return (        <div>          {data.slice(0, 2).map((item) => (            <p className="user-info">{item}</p>          ))}          <button className="more-info" onClick={() => setShowAll(!showAll)}>            + {data.length - 2} <FormattedMessage id={"more"} />            {/* {type}*/}          </button>        </div>      );    }  };  const Patterns = () => {    if (data.length <= 2) {      return data.map((item) => <p className="user-info">{item.pattern}</p>);    } else {      if (showAll) {        return (          <div>            {data.map((item) => (              <p className="user-info">{item.pattern}</p>            ))}            <button className="more-info" onClick={() => setShowAll(!showAll)}>              - <FormattedMessage id={"hide"} />{" "}              <span style={{ position: "relative", top: 2 }}>^</span>            </button>          </div>        );      }      return (        <div>          {data.slice(0, 2).map((item) => (            <p className="user-info">{item.pattern}</p>          ))}          <button className="more-info" onClick={() => setShowAll(!showAll)}>            + {data.length - 2} <FormattedMessage id={"more"} /> {type}          </button>        </div>      );    }  };  return (    <ContactBoxWrapper type={type} title={title}>      {type == "patterns" ? <Patterns /> : <Phones />}    </ContactBoxWrapper>  );}function ContactBoxWrapper({ children, type, title }) {  return (    <div className="contact-box">      <div className="cb-icon">        <img          className=""          src={window.chrome.runtime.getURL(            "/assets/images/" +              (type == "emails" ? "emailIcon.svg" : "phoneIcon.svg")          )}          alt={""}        />      </div>      <div className="cb-text">        <p className=""> {title}</p>        {children}      </div>    </div>  );}