import React, { useEffect, useState } from "react";import ReactDOM from "react-dom";import "./CustomToast.css";function MyToast({ children, style, show, closeColor, onClose,withoutClose }) {  return (    <div      id={"custom-toast"}      className={"custom-toast-fixed " + (show ? "show" : "")}      style={style}    >      {!withoutClose && <CloseIcon fill={closeColor} onClose={onClose}/>}      <div>{children}</div>    </div>  );}function CloseIcon({ fill, onClose, style }) {  return (    <span className={"close-icon"} style={style} onClick={onClose}>      <svg        aria-hidden="true"        focusable="false"        data-prefix="fas"        data-icon="times"        className="svg-inline--fa fa-times fa-w-11"        role="img"        xmlns="http://www.w3.org/2000/svg"        viewBox="0 0 352 512"      >        <path          fill={fill || "#fff"}          d="M242.72 256l100.07-100.07c12.28-12.28 12.28-32.19 0-44.48l-22.24-22.24c-12.28-12.28-32.19-12.28-44.48 0L176 189.28 75.93 89.21c-12.28-12.28-32.19-12.28-44.48 0L9.21 111.45c-12.28 12.28-12.28 32.19 0 44.48L109.28 256 9.21 356.07c-12.28 12.28-12.28 32.19 0 44.48l22.24 22.24c12.28 12.28 32.2 12.28 44.48 0L176 322.72l100.07 100.07c12.28 12.28 32.2 12.28 44.48 0l22.24-22.24c12.28-12.28 12.28-32.19 0-44.48L242.72 256z"        ></path>      </svg>    </span>  );}export default function CustomToast(props) {  const [show, setShow] = useState(false);  useEffect(() => {    if (props.toggle)      setTimeout(() => {        setShow(true);      }, 600);  }, []);  if (!props.toggle) return null;  if (props.isPortal) {    const extension_container = document.body.querySelector(      ".plugin-wrapper #KasprPlugin #ps-main"    );    return ReactDOM.createPortal(      <MyToast {...props} show={show} />,      extension_container    );  }  if (props.isStatic)    return (      <div        id={"custom-toast"}        className={"custom-toast-static " + (show ? "show " : "dnone")}        style={props.style}      >        {!props.withoutClose && (          <CloseIcon            fill={props.closeColor}            style={{              top: -3,              right: 5,              width: 8,            }}            onClose={props.onClose}          />        )}        <div>{props.children}</div>      </div>    );  return (    <div      id={"custom-toast"}      className={"custom-toast-absolute " + (show ? "show" : "")}      style={props.style}    >      {!props.withoutClose && (        <CloseIcon fill={props.closeColor} onClose={props.onClose} />      )}      <div>{props.children}</div>    </div>  );}