import React from "react";import { useCompanyData, useCompanyInfos } from "./Company.hooks";import CompanyEnrichment from "./CompanyEnrichment/CompanyEnrichment";import SalesNavigatorCompany from "./SalesNavigatorCompany/SalesNavigatorCompany";import {  AlreadyOpened,  CompanyBox,  CompanyWrapper,  CompanyDataNotFound,} from "./Common";import {  CompanyInfo,  ProfileDataNotFound,  ProfileWrapper,  ShowDetailsButton,} from "../Profile/Common";import { LoadingPage, Permissions } from "../../Components";import NoMoreCredits from "../Profile/Common/NoMoreCredits";import { REACT_APP_BASE_URL } from "../../../constants";import { FormattedMessage, useIntl } from "react-intl";import { usePermissions } from "../../Components/Permissions/permissions.hooks";export default function Company({  currentUrl,  toggle,  updateRemainingCredits,  organizationAdmin,  isPluginTop,  linkedinClasses,  credits,  memberPermissions,}) {  const { name, picture, id, previousId, isSalesNavigator, source } =    useCompanyInfos(currentUrl, isPluginTop, linkedinClasses);  const { state, handler } = useCompanyData(    id,    previousId,    name,    updateRemainingCredits,    source  );  const [checkSupportedWebsites] = usePermissions(    credits &&      credits.organizationCredits &&      credits.organizationCredits.plan &&      credits.organizationCredits.plan.permissions,    memberPermissions,    "SupportedWebsites"  );  const intl = useIntl();  if (!name || state.loading) {    return (      <CompanyWrapper isLoading>        <LoadingPage />      </CompanyWrapper>    );  }  if (state.errorMessage == "No credits left") {    return (      <ProfileWrapper picture={picture} name={name} job={""}>        <div style={{ textAlign: "center", marginTop: 70, fontWeight: 500 }}>          <NoMoreCredits            text={intl.formatMessage({ id: "sorryYouHaveNoMoreCredits" })}            subText={intl.formatMessage({ id: "pleaseUpgradeYourPlan" })}          />          <div className="btn-in btn-user">            <a              href={REACT_APP_BASE_URL + "/billing"}              target={"_blank"}              className="btn sm"              style={{                color: "#fff",                textAlign: "center",                display: "inline-block",              }}            >              <FormattedMessage id={"upgrade"} />            </a>          </div>        </div>      </ProfileWrapper>    );  }  if (    state.errorMessage ==    "Credit's limit reached, Contact your Admin to increase your limit"  ) {    return (      <ProfileWrapper picture={picture} name={name} job={""}>        <div style={{ textAlign: "center", marginTop: 70, fontWeight: 500 }}>          <NoMoreCredits            text={intl.formatMessage({              id: "sorryWeCouldNtFindContactDetails",            })}            subText={""}          />          <div className="btn-in btn-user">            <p              style={{                fontSize: 16,                width: "100%",                fontWeight: "normal",              }}            >              <FormattedMessage id={"pleaseContactYourAdmin"} />{" "}            </p>          </div>        </div>      </ProfileWrapper>    );  }  if (    state.errorMessage == "Only Admins can spend credits in a free organization"  ) {    return (      <ProfileWrapper picture={picture} name={name} job={""}>        <div style={{ textAlign: "center", marginTop: 70, fontWeight: 500 }}>          <NoMoreCredits            text={intl.formatMessage({              id: "onlyAdminsCanDecrease",            })}            subText={""}          />          <div className="btn-in btn-user">            <p              style={{                fontSize: 16,                width: "100%",                fontWeight: "normal",              }}            >              <FormattedMessage id={"pleaseContactYourAdmin"} />{" "}            </p>          </div>        </div>      </ProfileWrapper>    );  }  if (state.errorMessage) {    return (      <CompanyWrapper picture={picture} name={name}>        <CompanyDataNotFound />        {/* <CompanyInfo />*/}      </CompanyWrapper>    );  }  /*if (isSalesNavigator) {    return <SalesNavigatorCompany />;  }*/  return (    <CompanyWrapper picture={picture} name={name}>      {checkSupportedWebsites(source) ? (        <>          {state.data &&          state.data.discovered &&          !state.data.isFirstDiscover ? (            <AlreadyOpened toggle={toggle} />          ) : (            <ShowDetailsButton onClick={handler.onDiscover} />          )}        </>      ) : (        <Permissions.CallToAction          withoutIcon={true}          isPlan={true}          toolTipStyle={{ width: "100%" }}        >          {state.data &&          state.data.discovered &&          !state.data.isFirstDiscover ? (            <AlreadyOpened toggle={toggle} />          ) : (            <ShowDetailsButton onClick={handler.onDiscover} />          )}        </Permissions.CallToAction>      )}      <CompanyBox        title={intl.formatMessage({ id: "patterns" })}        type={"patterns"}        data={state.data && state.data.patterns}        discovered={state.data && state.data.discovered}      />      {state.data && state.data.phones && state.data.phones.length > 0 && (        <CompanyBox          title={intl.formatMessage({ id: "phoneNumbers" })}          type={"phones"}          data={state.data && state.data.phones}          discovered={state.data && state.data.discovered}        />      )}      {/*  <CompanyInfo />*/}      {state.data && state.data.companyInfo && (        <CompanyInfo companyInfo={state.data.companyInfo} />      )}    </CompanyWrapper>  );}