import React, { useEffect, useRef, useState } from "react";export default function SelectWithOptions({  title,  items,  onSelectItem,  value,  style,  dropDownStyle,  formInStyle,  setNewItems,  type,}) {  const [toggle, setToggle] = useState(false);  const [toggleNewItem, setToggleNewItem] = useState(false);  const ref = useRef(null);  useEffect(() => {    document.addEventListener("mousedown", handleClickOutside);    return () => document.removeEventListener("mousedown", handleClickOutside);  }, []);  function handleClickOutside(e) {    if (ref.current && !ref.current.contains(e.target)) {      setToggle(false);    }  }  const onDropDown = (e) => {    e.preventDefault();    setToggle(!toggle);  };  const handleChooseItem = (item) => {    onSelectItem(item);    setTimeout(() => {      setToggle(false);    }, 10);  };  const handleAddNewField = (value) => {    if (!value) return setToggleNewItem(false);    let fields = Object.assign([], items);    if (value.length < 0) return;    fields.push(value);    setToggleNewItem(false);    setNewItems({ values: fields, value });  };  const Item = ({ label, index }) => {    const [inputToggle, setInputToggle] = useState(null);    const [inputField, setInputField] = useState(label);    const [isValid, setFieldVerification] = useState(true);    const handleEditField = () => {      if (inputField == "") return setInputToggle(false);      let fields = Object.assign([], items);      if (type == "email") {        const regex =          /^[a-zA-Z0-9.!#$%&*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;        const isOk = regex.test(inputField);        if (!isOk) return setFieldVerification(false);      }      if (type == "phone") {        const regex =          /^[\+]?[(]?[0-9]{3}[)]?[-\s\.]?[0-9]{3}[-\s\.]?[0-9]{4,6}$/;        const isOk = regex.test(inputField);        if (!isOk) return setFieldVerification(false);      }      fields[index] = inputField;      setNewItems({ values: fields, value: inputField });      setInputToggle(false);    };    const handleDeleteField = (value) => {      let fields = Object.assign([], items);      fields = fields.filter((field) => field !== value);      setNewItems({ values: fields, value: "delete" });    };    return (      <div className="cdi-in" style={{ cursor: "default" }}>        <input type="radio" name="List" />        <p          className="mb-0"          style={{            display: "flex",            padding: "0 10px",            justifyContent: "space-between",          }}        >          {inputToggle == label ? (            <div className={"select-edit-field"}>              <input                type="text"                value={inputField}                onChange={(e) => {                  setFieldVerification(true);                  setInputField(e.target.value);                }}                style={!isValid ? { borderColor: "red" } : {}}              />              <button                onClick={handleEditField}                style={!isValid ? { background: "red" } : {}}              >                OK              </button>            </div>          ) : (            <span              style={{ cursor: "pointer" }}              onClick={() => {                setToggleNewItem(false);                handleChooseItem(label);              }}            >              {label}            </span>          )}          {inputToggle !== label && (            <div>              <img                src={window.chrome.runtime.getURL(                  "assets/images/edit-solid.svg"                )}                style={{                  width: 16,                  cursor: "pointer",                  marginRight: 7,                }}                alt=""                onClick={() => setInputToggle(label)}              />              <img                src={window.chrome.runtime.getURL(                  "assets/images/trash-alt-regular.svg"                )}                style={{                  width: 12,                  cursor: "pointer",                }}                alt=""                onClick={() => handleDeleteField(label)}              />            </div>          )}        </p>      </div>    );  };  const Items = ({ handleAddNewField }) => {    const [inputNewField, setInputNewField] = useState("");    const [isNewFieldValid, setNewFieldVerification] = useState(true);    const inputRef = useRef(null);    useEffect(() => {      if (inputRef.current && isNewFieldValid) {        inputRef.current.focus();      }    }, []);    const onAddNewField = () => {      if (inputNewField == "") return handleAddNewField(null);      if (type == "email") {        const regex =          /^[a-zA-Z0-9.!#$%&*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;        const isOk = regex.test(inputNewField);        if (!isOk) return setNewFieldVerification(false);      }      if (type == "phone") {        const regex =          /^[\+]?[(]?[0-9]{3}[)]?[-\s\.]?[0-9]{3}[-\s\.]?[0-9]{4,6}$/;        const isOk = regex.test(inputNewField);        if (!isOk) return setNewFieldVerification(false);      }      handleAddNewField(inputNewField);    };    return (      <div>        {items.map((label, i) => (          <Item label={label} index={i} />        ))}        {toggleNewItem ? (          <div className="cdi-in" style={{ cursor: "default" }}>            <input type="radio" name="List" />            <div className={"select-edit-field"}>              <input                type="text"                defaultValue={inputNewField}                style={                  !isNewFieldValid                    ? { borderColor: "red", width: "100%" }                    : { width: "100%" }                }                ref={inputRef}                onChange={(e) => {                  setNewFieldVerification(true);                  setInputNewField(e.target.value);                }}              />              <button                style={!isNewFieldValid ? { background: "red" } : {}}                onClick={onAddNewField}              >                OK              </button>            </div>          </div>        ) : (          <div>            <button              className={"btn-white"}              style={{ fontSize: 14 }}              onClick={() => setToggleNewItem(true)}            >              Add a {title}{" "}            </button>          </div>        )}      </div>    );  };  return (    <div className="form-in" ref={ref} style={formInStyle || {}}>      <label>{title}</label>      <div className={"custom-dropdown workflow " + (toggle ? "show" : "")}>        <button className="custom-btn" onClick={onDropDown} style={style || {}}>          {value}        </button>        <div          className="custom-dropdown-in drop-down-style "          style={dropDownStyle || {}}        >          <Items handleAddNewField={handleAddNewField} />        </div>      </div>    </div>  );}